const setHeaderStyles = (gmbClass) => {
  const styles = `
    .gmb-admin-section-tiles:hover {
      background-color: rgb(255, 184, 165) !important;
    }

    .${gmbClass}:hover .${gmbTooltipClass} {
        opacity: 1;
        transition: opacity 0.25s linear;
        transform: translate(-50%, 0);
    }
    
    .${gmbClass} .${gmbTooltipClass} {
        position: absolute;
        left: 50%;
        bottom: 100%;
        opacity: 0;
        margin-bottom: 0.5rem;
        padding: 0.25rem 0.5rem;
        border-radius: 0.25rem;
        font-weight: 400;
        line-height: 1.5;
        text-decoration: none;
        text-shadow: none;
        text-transform: none;
        text-align: center;
        letter-spacing: normal;
        word-break: normal;
        word-spacing: normal;
        white-space: normal;
        line-break: auto;
        word-wrap: break-word;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
        "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
        "Segoe UI Symbol", "Noto Color Emoji";
        font-size: 0.875rem;
        color: #fff;
        background-color: #191919;
        max-width: 200px;
    }
    
    .${gmbClass} .${gmbTooltipClass}::before {
        content: '';
        position: absolute;
        top: 100%;
        left: 50%;
        width: 0;
        height: 0;
        border: 0.5em solid transparent;
        border-top-color: #000;
        transform: translate(-50%, 0);
    }

    @keyframes ${gmbClass}_loader_spin {
      0% { transform: rotate(0deg); }
      100% { transform: rotate(360deg); }
    }`;
  document.head.appendChild(document.createElement("style")).innerHTML = styles;
};

const returnEnumOfCodeMode = () => {
  const HowCorrectCategoryFoundEnum = {
    GMBScriptOnPage: 1,
    GMBScriptWithDirectLocation: 2,
    GMBScriptWithNearLocation: 3,
    NotFound: 4,
    ExactLink: 5
  };
  Object.freeze(HowCorrectCategoryFoundEnum);
  return HowCorrectCategoryFoundEnum;
}


const addTooltip = (gmbContainer) => {
  const tooltip = document.createElement("span");
  tooltip.setAttribute("class", gmbTooltipClass);
  tooltip.innerHTML = "By GMB Everywhere.";

  gmbContainer.appendChild(tooltip);
};

const  pickupScriptStringFromPage = () => {
  const onPageHTML = document.documentElement.innerHTML;

  const searchString = "window.APP_INITIALIZATION_STATE=";
  const codeStart = onPageHTML.indexOf(searchString);
  const codeEnd = onPageHTML.indexOf(
    "window.APP_FLAGS",
    codeStart
  );
  const code = onPageHTML.substring(
    codeStart + searchString.length,
    codeEnd
  );

  //slice the string to remove simicolon before parsing
  const WindowInitializationState = JSON.parse(code.slice(0, -1));
  const processedTxt = WindowInitializationState && WindowInitializationState[3] ? WindowInitializationState[3][6] : "";
  return processedTxt;
}

let countNumberOfTimeValueExistInArray = (value, array) => {
  var count = 0;
  array.forEach((v) => (v === value && count++));
  return count;
}

const getCurrentUrl = () => window.location.href;


let returnUniqueValuesInArray = (array) => array.filter((item, i, ar) => ar.indexOf(item) === i);

