const truncateText = (text, limit) => {
  if (text.length > limit) {
     return `${text.substring(0, limit)}...`;
  }
  return text;
}

const getReviewAuditModalVersion = () => {
  // review modal scoll container
  if(document.querySelector('[data-sort-id="newestFirst"]')) {
    return REVIEW_AUDIT_MODAL_VERSION.v1;
  }

  // review modal scoll container
  if(document.querySelector('g-scrolling-carousel[jsshadow] div[data-sort="2"]')) {
    return REVIEW_AUDIT_MODAL_VERSION.v2;
  }

  return REVIEW_AUDIT_MODAL_VERSION.v1;
}

const checkIfElementExists = (elementOrSelector, intervalTime, maxAttempts, onSuccess, onFailure) => {
  let attempts = 0;
  const interval = setInterval(function () {
    let element;

    if (typeof elementOrSelector === 'string') {
      element = document.querySelector(elementOrSelector);
    } else if (typeof elementOrSelector === 'function') {
      element = elementOrSelector();
    }

    if (element) {
      clearInterval(interval);
      if (onSuccess) onSuccess(element);
    }

    attempts++;
    if (attempts >= maxAttempts) {
      clearInterval(interval);
      if (onFailure) onFailure(new Error(`Element ${elementOrSelector} not found after ${maxAttempts} attempts.`));
    }
  }, intervalTime);
};
const apiPostRequest = (url, data) => {
  return new Promise((resolve, reject) => {
      fetch(url, {
          method: 'POST',
          headers: {
              'Content-Type': 'application/json',
          },
          body: JSON.stringify(data)
      })
          .then(response => {
              if (!response.ok) {
                  throw new Error(`HTTP error! status: ${response.status}`);
              }
              return response.json();
          })
          .then(data => resolve(data))
          .catch(error => {
              console.error('Error making POST request:', error);
              reject(error);
          })
  })
}

const createGuid = () => crypto.randomUUID();

