import { createLocalScanButton, onError, processAndAnalyzeBusinesses } from "./audit-gmb/local-scan-logic";
import { flag } from "./feature-flag-and-constants";

const attachLocalScanToGoogleSearchResult = (businessContainers) => {
    if (flag.googleSearchResultPageLocalScan) {
        appendLocalScanButton(businessContainers);
    }
}


const extractBusinessData = (businessContainers) => {
    const businessses = [];
    let rank = 0;
    businessContainers.forEach((businessContainer) => {
        if(isAdsBusiness(businessContainer)) return;

        const businessName = businessContainer.querySelector("div > div[role='heading'] > span")?.textContent
        const cid = businessContainer.getAttribute('data-cid');
        businessses.push({
            businessName,
            cid,
            rank: ++rank,
            businessUrl: "https://" + window.location.hostname + "/maps?cid=" + cid,
        });
    });
    return businessses;
}

const isAdsBusiness = (businessElement) => {
    const container = businessElement.querySelector("div>div>div");

    // this is for the images that shows before the business name
    // if it's null, it's not a business but just an image
    if(container === null) return false;

    // verify if it's a sponsored business
    // sponsored business has no role attribute on this div, it's after the sponsored container div
    // non sponsored business has role attribute on this div, as it contains the business name
    if(container.hasAttribute("role") && container.getAttribute("role") === "heading") {
        return false;
    }

    return true;
}

const appendLocalScanButton = (businessContainers) => {
    const container = document.querySelector("div[data-hveid] > div[id] div[data-vstcp='1']")?.previousElementSibling
    if (container) {
        container.insertBefore(getLocalScanButton(businessContainers), container.firstChild.nextSibling);
    }
}

const getLocalScanButton = (businessContainers) => {
    const onClick = (businessContainers) => {
        const businessses = extractBusinessData(businessContainers);
        if(businessses.length > 0) {
            processAndAnalyzeBusinesses(businessses);
            return;
        }

        onError("No businesses found");
    }

    return createLocalScanButton(() => onClick(businessContainers));
}

export { attachLocalScanToGoogleSearchResult };
