import * as LocalFinder from "../js/local-finder";
import * as Logger from "../js/logger";
import * as Utility from "../js/utility";
import * as AuditInformation from "./audit-gmb/audit-extension-code";
import { findBusinessRankForTeleport } from "./audit-gmb/find-business-rank";
import { appendLocalScanButton } from "./audit-gmb/local-scan-logic";
import * as RetrievePost from "./audit-gmb/scrape-posts";
import * as RetrieveReviews from "./audit-gmb/scrape-reviews";
import * as FlagAndConstants from "./feature-flag-and-constants";
import { attachLocalScanToGoogleSearchResult } from "./google-search";

(function () {
  let propertyName = "";

  const HowCorrectCategoryFoundEnum = Utility.returnEnumOfCodeMode();

  let propertyHowAccessedCategories = HowCorrectCategoryFoundEnum.NotFound;
  let searchResultItemsArray = [];
  let existingBusinessListOnPage = [];
  let singlePageDetailsArray = [];
  const MsgNoCategory = "GMB categories not found.";
  const MsgExternalCallNoCategory =
    "GMB categories not found. Use the 'Share' link below to go to the business page to view the categories.";
  const loadingTxt = "[loading]";
  const msgSearchPageCategoriesNotFound = "GMB category not found";
  const searchPageCategoryElementClass = "category-list-display";
  let isContinueExecuting = true;

  try {
    const isSamePropertyName = () => propertyName === getCurrentPropertyName();

    const getCurrentReviewRatingAndNumberOfReviews = () => {
      let reviewSection;
      if (document.querySelectorAll('[jsaction="pane.rating.moreReviews"]').length > 1) {
        reviewSection = document.querySelectorAll('[jsaction="pane.rating.moreReviews"]')[0].innerText;
      } 
      else if (document.querySelector('[jsaction$=".category"]')) {
        // this is for businesses in map search result and single business page using "share" link
        // this targest category of the business and goes up the DOM tree to the section rating element
        reviewSection = document.querySelector('[jsaction$=".category"]').parentNode.parentNode.parentNode.parentNode.innerText;
      }
      else {
        reviewSection = document.querySelectorAll('[jsaction="pane.rating.category"]')[0].parentNode.parentNode.parentNode.parentNode.firstChild.children[1].innerText;
      }

      if (!reviewSection) {
        return {
          "average-review-rating": 0,
          "number-of-reviews": 0,
        };
      }
      const numberOfReviews = reviewSection.split("\n")[1].split(" ")[0].replace(/\D/g, "");
      return {
        "average-review-rating": reviewSection.split("\n")[0].replace(",", "."),
        "number-of-reviews": parseInt(numberOfReviews)
      };
    };

    const getCurrentPropertyName = () => {
      // var elem = document.querySelector(".fontHeadlineLarge");
      var elem = document.querySelector('h1.DUwDvf.lfPIob');
      if (elem) {
        return elem.innerText.trim();
      }

      try {
        elem = document.querySelector('.google-symbols')
        .parentNode.parentNode.parentNode.parentNode.parentNode
        .querySelector("div")
        .nextSibling
        .querySelector(":scope div > div > div")
        .querySelector(":scope > h1")

        if (elem) {
          return elem.innerText.trim();
        }
      } catch {}
    };

    function GetValuesFromPageScript(windowArray) {
      let categories = [];
      let businessName = "";
      let phoneNumber = "";
      let averageReviewRating, averageNumberOfReviews;
      /*
      wrapped with try catch as there's a possibility that the category list is on different index.
      so far, only 2 locations ecountered.
    */
      try {
        categories = windowArray[0][1][0][14][13];
        businessName = windowArray[0][1][0][14][11];
        if (windowArray[0][1][0][14][178] != null) {
          phoneNumber = windowArray[0][1][0][14][178][0][3];
        } else {
          phoneNumber = "";
        }
        if (windowArray[0][1][0][14][4] != null) {
          averageReviewRating = windowArray[0][1][0][14][4][7];
          averageNumberOfReviews = windowArray[0][1][0][14][4][8];
        } else {
          averageReviewRating = 0;
          averageNumberOfReviews = 0;
        }
      } catch {}

      try {
        categories = windowArray[0][1][1][14][13];
        businessName = windowArray[0][1][1][14][11];
        if (windowArray[0][1][1][14][178] != null) {
          phoneNumber = windowArray[0][1][1][14][178][0][3];
        } else {
          phoneNumber = "";
        }
        if (windowArray[0][1][1][14][4] != null) {
          averageReviewRating = windowArray[0][1][1][14][4][7];
          averageNumberOfReviews = windowArray[0][1][1][14][4][8];
        } else {
          averageReviewRating = 0;
          averageNumberOfReviews = 0;
        }
      } catch {}
      return {
        categories,
        phoneNumber,
        businessName,
        averageNumberOfReviews,
        averageReviewRating,
      };
    }

    const getSectionRatingElement = () => {
      if (document.querySelectorAll('[jsaction="pane.rating.moreReviews"]').length > 1) {
        return document.querySelectorAll('[jsaction="pane.rating.moreReviews"]')[0].parentNode.parentNode.parentNode;
      } else if (document.querySelector('a[href="#"][target][jsaction]>div>div') !== null) {
        // this is for hotel businesses in map search results and single business page using "share" link
        // this targets the "Check Availability" button and goes up the DOM tree to the section rating element
        return document.querySelector('a[href="#"][target][jsaction]>div>div').parentNode.parentNode.parentNode.parentNode.parentNode.firstChild.nextSibling.firstChild.firstChild.firstChild.nextSibling.firstChild;
      } else if (document.querySelector('[jsaction$=".category"]')) {
        // this is for businesses in map search result and single business page using "share" link
        // this targest category of the business and goes up the DOM tree to the section rating element
        return document.querySelector('[jsaction$=".category"]').parentNode.parentNode.parentNode.parentNode.parentNode;
      }
      else {
        return document.querySelectorAll('[jsaction="pane.rating.category"]')[0].parentNode.parentNode.parentNode.parentNode.parentNode;
      }
    }

    const getGmbCategoryContainer = () =>
      getSectionRatingElement().querySelector(
          "." + Utility.gmbCategoryClass
        );

    const getBusinessLocation = () => {
      try {
        var elem = document.querySelector('[data-item-id="address"]').innerText;
        if (elem) {
          return elem.trim();
        }
      } catch {
        return "";
      }
    };

    const removeGMBElement = () => {
      try {
        const gmbCategoryContainer = getGmbCategoryContainer();

        if (gmbCategoryContainer) {
          gmbCategoryContainer.remove();
        }
      } catch (e) {}
    };

    const modifyPage = () => {
      let placeHolder;

      try {
        placeHolder = getSectionRatingElement();
      } catch (e) {}
      if (placeHolder) {
        fetchCategories();
      }
    };

    let auditButtonOnClickLogic = (buttonObject, auditType) => {
      buttonObject.onclick = function () {
        try {
          buttonObject.disabled = true;
          setTimeout(function () {
            buttonObject.disabled = false;
          }, 3000);

          if (
            propertyHowAccessedCategories ===
            HowCorrectCategoryFoundEnum.GMBScriptOnPage
          ) {
            AuditInformation.AuditInformationLogic(
              window.location.href,
              propertyHowAccessedCategories,
              HowCorrectCategoryFoundEnum,
              auditType
            );
          } else if (
            propertyHowAccessedCategories ===
            HowCorrectCategoryFoundEnum.GMBScriptWithDirectLocation
          ) {
            AuditInformation.AuditInformationLogic(
              generateSearchUrlByLocation(),
              propertyHowAccessedCategories,
              HowCorrectCategoryFoundEnum,
              auditType
            );
          } else if (
            propertyHowAccessedCategories ===
            HowCorrectCategoryFoundEnum.GMBScriptWithNearLocation
          ) {
            AuditInformation.AuditInformationLogic(
              generateSearchUrlByNearLocation(),
              propertyHowAccessedCategories,
              HowCorrectCategoryFoundEnum,
              auditType
            );
          } else {
            throw new Error("value not found");
          }
        } catch (e) {
          document.getElementById("errorMessageButtonClick").innerText =
            "An error occured. Sorry for the inconvenience.";

          Utility.forwardToErrorPage({
            auditType: auditType,
            error: e
          });
          return;
        }
      };
    };

    const showButtonAndAddToEnd = (parent) => {
      let buttonBasicAudit, buttonReviewAudit;
      const container = document.createElement("div");
      container.id = "popupButtonAdditionalInformationDiv";
      container.style.cssText = `text-align: left;
      display: flex;
      flex-wrap: wrap;
      margin-top: 10px;`;

      if (FlagAndConstants.flag.mapPageBasicAudit) {
        let buttonBasicAudit = Utility.createAuditDisplayButton(
          `${Utility.createEmojiIcon({
            iconName: "rocket_icon.svg",
            iconSize: "14px",
          })} Basic Audit`,
          "popupButtonAdditionalInformation",
          {
            style: `flex-basis: 48%;
          margin-bottom: 0 !important;
          margin-top: 10px !important;`,
          }
        );
        auditButtonOnClickLogic(buttonBasicAudit, "basic-audit");
        container.appendChild(buttonBasicAudit);
      }

      if (FlagAndConstants.flag.mapPageAIAudit) {
        let buttonAIAudit = Utility.createAuditDisplayButton(
          `${Utility.createEmojiIcon({
            iconName: "brain_icon.svg",
            iconSize: "15px",
          })} 
          AI`,
          "popupAIAudit",
          {
            style: `flex-basis: 48%;
            margin-bottom: 0;
            margin-top: 10px;`,
          }
        );
        buttonAIAudit.style.width = "100px";
        auditButtonOnClickLogic(buttonAIAudit, "ai-audit");
        container.appendChild(buttonAIAudit);
      }

      if (FlagAndConstants.flag.mapPageTeleport) {
        let buttonTeleport = Utility.createAuditDisplayButton(
          `${Utility.createEmojiIcon({
            iconName: "pin_icon.svg",
            iconSize: "14px",
          })} Teleport`,
          "popupTeleport",
          {
            style: `flex-basis: 48%;
          margin-bottom: 0;
          margin-top: 10px;`,
          }
        );
        auditButtonOnClickLogic(buttonTeleport, "teleport");
        container.appendChild(buttonTeleport);
      }

      if (FlagAndConstants.flag.mapPageReviewAudit) {
        let buttonReviewAudit = Utility.createAuditDisplayButton(
          `${Utility.createEmojiIcon({
            iconName: "half_star_icon.svg",
            iconSize: "14px",
          })} Review Audit`,
          "popupReviewAudit",
          {
            style: `flex-basis: 48%;
          margin-bottom: 0;
          margin-top: 10px;`,
          }
        );
        buttonReviewAudit.style.width = "120px";
        auditButtonOnClickLogic(buttonReviewAudit, "review-audit");
        container.appendChild(buttonReviewAudit);
      }

      if (FlagAndConstants.flag.mapPagePostAudit) {
        let buttonPostAudit = Utility.createAuditDisplayButton(
          `${Utility.createEmojiIcon({
            iconName: "megaphone_icon.svg",
            iconSize: "14px",
          })} Post Audit`,
          "popupPostAudit",
          {
            style: `flex-basis: 48%;
          margin-bottom: 0;
          margin-top: 10px;`,
          }
        );
        buttonPostAudit.style.width = "100px";
        auditButtonOnClickLogic(buttonPostAudit, "post-audit");
        container.appendChild(buttonPostAudit);
      }

      const errorMessageDiv = document.createElement("div");
      errorMessageDiv.id = "errorMessageButtonClick";

      container.appendChild(errorMessageDiv);

      parent.appendChild(container);
    };

    const getCurrentDomainHostName = () => window.location.hostname;

    const generateSearchUrlByLocation = () => {
      const domainHostName = getCurrentDomainHostName();
      const encodedParam = encodeURI(
        `${propertyName} ${getBusinessLocation()}`
      );
      const url = `https://${domainHostName}/maps/search/${encodedParam}`;
      return url;
    };

    const generateSearchUrlByNearLocation = () => {
      const domainHostName = getCurrentDomainHostName();
      const encodedParam = encodeURI(
        `${propertyName}&near=${getBusinessLocation()}`
      );
      const url = `https://${domainHostName}/maps?q=${encodedParam}`;

      return url;
    };

    const fetchCategoriesWithDelay = (fetch, maxLimitInSec) => {
      var delayInMs = Math.floor(Math.random() * maxLimitInSec * 1000);
      setTimeout(() => {
        fetch();
      }, delayInMs);
    };

    const callGoogleMap = (url, callback) => {
      const xhr = new XMLHttpRequest();
      xhr.onreadystatechange = () => {
        if (xhr.readyState === XMLHttpRequest.DONE) {
          const status = xhr.status;
          if (status === 0 || (status >= 200 && status < 400)) {
            try {
              const searchString = "window.APP_INITIALIZATION_STATE=";
              const codeStart = xhr.response.indexOf(searchString);
              const codeEnd = xhr.response.indexOf(
                "window.APP_FLAGS",
                codeStart
              );
              const code = xhr.response.substring(
                codeStart + searchString.length,
                codeEnd
              );

              //slice the string to remove simicolon before parsing
              const WindowInitializationState = JSON.parse(code.slice(0, -1));
              callback(WindowInitializationState);
            } catch {
              callback("");
            }
          } else {
            return "";
          }
        }
      };

      //Request data from server(google map) to parse APP_INITIALIZATION_STATE value
      xhr.open("GET", url);
      xhr.send();
    };

    const fetchCategories = () => {
      try {
        const processedTxt = Utility.pickupScriptStringFromPage();
        if (processedTxt && processedTxt.length > 5) {
          const windowArray = JSON.parse(processedTxt.substring(5));
          const categories = windowArray[6][13];

          const businessNameFromCodeEmbedded = windowArray[6][11];

          if (
            categories &&
            categories.length &&
            getCurrentPropertyName().trim() ===
              businessNameFromCodeEmbedded.trim()
          ) {
            propertyHowAccessedCategories =
              HowCorrectCategoryFoundEnum.GMBScriptOnPage;

            setGmbCategory(categories);
          } else {
            fetchCategoriesWithDelay(
              fetchExternalSourceCategoriesByLocation,
              1
            );
          }
        } else {
          fetchCategoriesWithDelay(fetchExternalSourceCategoriesByLocation, 1);
        }
      } catch {
        fetchCategoriesWithDelay(fetchExternalSourceCategoriesByLocation, 1);
      }
    };

    const fetchExternalSourceCategoriesByLocation = () => {
      callGoogleMap(generateSearchUrlByLocation(), (data) => {
        try {
          const processedTxt = data && data[3] ? data[3][2] : "";

          if (processedTxt && processedTxt.length > 5) {
            const windowArray = JSON.parse(processedTxt.substring(5));

            let {
              categories,
              phoneNumber,
              businessName,
              averageNumberOfReviews,
              averageReviewRating,
            } = GetValuesFromPageScript(windowArray);

            if (
              categories &&
              categories.length &&
              getCurrentPropertyName().trim() === businessName.trim() &&
              averageNumberOfReviews ===
                getCurrentReviewRatingAndNumberOfReviews()[
                  "number-of-reviews"
                ] &&
              parseInt(averageReviewRating * 100) ===
                parseInt(
                  getCurrentReviewRatingAndNumberOfReviews()[
                    "average-review-rating"
                  ] * 100
                )
            ) {
              propertyHowAccessedCategories =
                HowCorrectCategoryFoundEnum.GMBScriptWithDirectLocation;
              setGmbCategory(categories);
            } else {
              fetchCategoriesWithDelay(
                fetchExternalSourceCategoriesByNearLocation,
                1
              );
            }
          } else {
            fetchCategoriesWithDelay(
              fetchExternalSourceCategoriesByNearLocation,
              1
            );
          }
        } catch {
          fetchCategoriesWithDelay(
            fetchExternalSourceCategoriesByNearLocation,
            1
          );
        }
      });
    };

    const fetchExternalSourceCategoriesByNearLocation = () => {
      callGoogleMap(generateSearchUrlByNearLocation(), (data) => {
        try {
          const processedTxt = data && data[3] ? data[3][2] : "";

          if (processedTxt && processedTxt.length > 5) {
            const windowArray = JSON.parse(processedTxt.substring(5));

            let {
              categories,
              phoneNumber,
              businessName,
              averageNumberOfReviews,
              averageReviewRating,
            } = GetValuesFromPageScript(windowArray);

            if (
              categories &&
              categories.length &&
              getCurrentPropertyName().trim() === businessName.trim() &&
              averageNumberOfReviews ===
                getCurrentReviewRatingAndNumberOfReviews()[
                  "number-of-reviews"
                ] &&
              parseInt(averageReviewRating * 100) ===
                parseInt(
                  getCurrentReviewRatingAndNumberOfReviews()[
                    "average-review-rating"
                  ] * 100
                )
            ) {
              propertyHowAccessedCategories =
                HowCorrectCategoryFoundEnum.GMBScriptWithNearLocation;

              setGmbCategory(categories);
            } else {
              setGmbCategoryValue(MsgExternalCallNoCategory);
            }
          } else {
            setGmbCategoryValue(MsgExternalCallNoCategory);
          }
        } catch {
          setGmbCategoryValue(MsgExternalCallNoCategory);
        }
      });
    };

    const setGmbCategoryValue = (content) => {
      try {
        removeGMBElement();

        if (content === loadingTxt) {
          showLoading(getSectionRatingElement());
        } else {
          showText(getSectionRatingElement(), content);
        }
      } catch (e) {
        removeGMBElement();
        Logger.error(e);
      }
    };

    const showLoading = (parent) => {
      const img = document.createElement("img");
      img.setAttribute("class", Utility.gmbCategoryClass);
      img.src = chrome.runtime.getURL("imgs/loading.gif");
      img.style.cssText =
        "height: 35px; margin-left: 0; margin-top: -8px; display: block; margin-top: 7px;";
      parent.appendChild(img);
    };

    const showText = (parent, content) => {
      const node = document.createElement("p");
      node.setAttribute("class", Utility.gmbCategoryClass);
      node.style.cssText =
        "display: inline-block; font-size: 14px; border-radius: .25rem; color: #212529;";
      node.appendChild(document.createTextNode(content));
      parent.appendChild(node);
    };

    const setGmbCategory = (catArr) => {
      try {
        removeGMBElement();

        const container = document.createElement("div");
        container.className = Utility.gmbCategoryClass;
        container.style.cssText = "position: relative; margin-top: 7px;";
        Utility.addTooltip(container);

        const title = document.createElement("span");
        title.textContent = "GMB Categories: ";
        title.style.cssText =
          "display: inline-block; font-size: 14px; border-radius: .25rem; color: #212529;";
        container.appendChild(title);

        catArr.forEach((v, i) => {
          let category = document.createElement("span");
          category.innerHTML =
            (i === 0
              ? Utility.createEmojiIcon({
                  iconName: "star_icon.svg",
                  iconSize: "12px",
                })
              : "") + v;
          category.style.cssText =
            "display: inline-block;" +
            "padding: .25em .4em;" +
            "font-size: 14px;" +
            "line-height: 1;" +
            "text-align: center;" +
            "white-space: nowrap;" +
            "vertical-align: baseline;" +
            "border-radius: .25rem;" +
            "color: #212529;" +
            "background-color: #ffc8b9;" +
            "margin: 0 0 3px 3px;";
          container.appendChild(category);
        });

        Utility.appendCategoryTool(container, catArr);

        showButtonAndAddToEnd(container);
        getSectionRatingElement().appendChild(container);
      } catch (e) {
        removeGMBElement();
        Logger.error(e);
      }
    };

    //-------- Search result. Multiple Businesses ---------------

    const removeGMBElementFromSearchPage = (elementDetails) => {
      const gmbCategoryContainer = elementDetails.querySelector(
        "." + searchPageCategoryElementClass
      );

      if (gmbCategoryContainer) {
        gmbCategoryContainer.remove();
      }
    };

    const displayNotFoundMessageOnSearchResultPage = (
      businessName,
      listOfElementWithBusinessDetails
    ) => {
      for (let i = 0; i < listOfElementWithBusinessDetails.length; i++) {
        if (
          listOfElementWithBusinessDetails[i].innerText.trim() === businessName
        ) {
          removeGMBElementFromSearchPage(
            listOfElementWithBusinessDetails[i].parentNode.parentNode
          );

          const container = document.createElement("div");
          container.className = searchPageCategoryElementClass;
          container.style.cssText = "margin-top: 7px;";

          const title = document.createElement("span");
          title.textContent = msgSearchPageCategoriesNotFound;
          title.style.cssText =
            "display: inline-block; font-size: 12px; border-radius: .15rem; color: #212529;";
          container.appendChild(title);
          listOfElementWithBusinessDetails[i].parentNode.parentNode.append(
            container
          );
        }
      }
    };

    const searchPageAuditButtonDesigns = () => {
      const spanSectionForButton = document.createElement("div");
      spanSectionForButton.style.cssText = "text-align: left;";
      const buttonDesign = (buttonText, onClickFunction) => {
        let buttonDesign = document.createElement("button");
        buttonDesign.style.cssText =
          "margin-top: 3px; margin-bottom: 3px; width: 70px; margin-right: 2px; line-height: 1.5; border: 2px solid rgb(204, 77, 41); background-color: rgb(255, 200, 185); color: rgb(33, 37, 41); font-size: 12px; padding: 3px 2px; display: inline-block; border-radius: 3px; cursor: pointer;";
        buttonDesign.innerHTML = buttonText;
        buttonDesign.onclick = onClickFunction;
        return buttonDesign;
      };

      const returnURLValueAssigned = (element) =>
        element.parentElement.parentElement.parentElement.parentElement
          .firstChild.href;

      if (FlagAndConstants.flag.mapSearchPageBasicAudit) {
        spanSectionForButton.appendChild(
          buttonDesign(
            `${Utility.createEmojiIcon({
              iconName: "rocket_icon.svg",
              iconSize: "12px",
            })} Basic`,
            function () {
              AuditInformation.AuditInformationLogic(
                returnURLValueAssigned(this),
                HowCorrectCategoryFoundEnum.ExactLink,
                HowCorrectCategoryFoundEnum,
                "basic-audit"
              );
            }
          )
        );
      }

      if (FlagAndConstants.flag.mapSearchPageAIAudit) {
        spanSectionForButton.appendChild(
          buttonDesign(
            `${Utility.createEmojiIcon({
              iconName: "brain_icon.svg",
              iconSize: "13px",
            })} 
            AI`,
            function () {
              AuditInformation.AuditInformationLogic(
                returnURLValueAssigned(this),
                HowCorrectCategoryFoundEnum.ExactLink,
                HowCorrectCategoryFoundEnum,
                "ai-audit"
              );
            }
          )
        );
      }

      if (FlagAndConstants.flag.mapPageTeleport) {
        spanSectionForButton.appendChild(
          buttonDesign(
            `${Utility.createEmojiIcon({
              iconName: "pin_icon.svg",
              iconSize: "12px",
            })} Teleport`,
            function () {
              AuditInformation.AuditInformationLogic(
                returnURLValueAssigned(this),
                HowCorrectCategoryFoundEnum.ExactLink,
                HowCorrectCategoryFoundEnum,
                "teleport"
              );
            }
          )
        );
      }

      if (FlagAndConstants.flag.mapSearchPageReviewAudit) {
        spanSectionForButton.appendChild(
          buttonDesign(
            `${Utility.createEmojiIcon({
              iconName: "half_star_icon.svg",
              iconSize: "12px",
            })} Review`,
            function () {
              AuditInformation.AuditInformationLogic(
                returnURLValueAssigned(this),
                HowCorrectCategoryFoundEnum.ExactLink,
                HowCorrectCategoryFoundEnum,
                "review-audit"
              );
            }
          )
        );
      }

      if (FlagAndConstants.flag.mapPagePostAudit) {
        spanSectionForButton.appendChild(
          buttonDesign(
            `${Utility.createEmojiIcon({
              iconName: "megaphone_icon.svg",
              iconSize: "12px",
            })} Post`,
            function () {
              AuditInformation.AuditInformationLogic(
                returnURLValueAssigned(this),
                HowCorrectCategoryFoundEnum.ExactLink,
                HowCorrectCategoryFoundEnum,
                "post-audit"
              );
            }
          )
        );
      }

      return spanSectionForButton;
    };

    // <div id="popupButtonAdditionalInformationDiv" style="text-align: left;"><button id="popupButtonAdditionalInformation" style="margin-top: 20px; margin-bottom: 20px; width: 150px; margin-right: 7px; line-height: 1.5; border: 2px solid rgb(204, 77, 41); background-color: rgb(255, 200, 185); color: rgb(33, 37, 41); font-size: 14px; padding: 7px 20px; display: inline-block; border-radius: 3px; cursor: pointer;"> Basic Audit</button><button id="popupReviewAudit" style="margin-top: 20px; margin-bottom: 20px; width: 150px; margin-right: 7px; line-height: 1.5; border: 2px solid rgb(204, 77, 41); background-color: rgb(255, 200, 185); color: rgb(33, 37, 41); font-size: 14px; padding: 7px 20px; display: inline-block; border-radius: 3px; cursor: pointer;"> Review Audit</button><div id="errorMessageButtonClick"></div></div>

    const displayCategoriesOnSearchResultPage = (
      businessName,
      categoryArray,
      listOfElementWithBusinessDetails
    ) => {
      for (let i = 0; i < listOfElementWithBusinessDetails.length; i++) {
        if (
          listOfElementWithBusinessDetails[i].innerText.trim() === businessName
        ) {
          let elementWithBusinessDetails =
            listOfElementWithBusinessDetails[i].parentNode.parentNode.parentNode
              .parentNode.parentNode.parentNode.parentNode;
          removeGMBElementFromSearchPage(elementWithBusinessDetails);

          const container = document.createElement("div");
          container.className = searchPageCategoryElementClass;
          container.style.cssText =
            "position: relative; margin-top: 7px; padding-left: 24px; padding-right: 6px;";

          const title = document.createElement("span");
          title.textContent = "GMB Cat.: ";
          title.style.cssText =
            "display: inline-block; font-size: 12px; border-radius: .15rem; color: #212529;";
          container.appendChild(title);

          categoryArray.forEach((v, i) => {
            let category = document.createElement("span");
            category.innerHTML =
              (i === 0
                ? Utility.createEmojiIcon({
                    iconName: "star_icon.svg",
                    iconSize: "10px",
                  })
                : "") + v;
            category.style.cssText =
              "display: inline-block;" +
              "padding: .25em .25em;" +
              "font-size: 12px;" +
              "line-height: 1;" +
              "text-align: center;" +
              "white-space: nowrap;" +
              "vertical-align: baseline;" +
              "border-radius: .15rem;" +
              "color: #212529;" +
              "background-color: #ffc8b9;" +
              "margin: 0 0 1px 1px;";
            container.appendChild(category);
          });

          Utility.appendCategoryTool(container, categoryArray, FlagAndConstants.gmbSearchType.mapSearchResultPane);
          
          container.appendChild(searchPageAuditButtonDesigns());

          elementWithBusinessDetails.appendChild(
            container
          );
        }
      }
    };

    // This adds the search symbol to the result page.
    const searchResultCheckIfLoadingSymbolThereOrAddIt = (elementList) => {
      let img;
      for (let i = 0; i < elementList.length; i++) {
        img = document.createElement("img");
        img.setAttribute("class", searchPageCategoryElementClass);
        img.src = chrome.runtime.getURL("imgs/loading.gif");
        img.style.cssText =
          "height: 35px; margin-left: 0; margin-top: -8px; display: block; margin-top: 7px; padding-left:24px; padding-right: 6px;";
        let elementSectionForDetails =
          elementList[i].parentNode.parentNode.parentNode.parentNode.parentNode
            .parentNode.parentNode;
        if (
          elementSectionForDetails.querySelector(
            "." + searchPageCategoryElementClass
          ) === null
        ) {
          try {
            elementSectionForDetails.insertBefore(
              img,
              elementSectionForDetails.nextSibling
            );
          } catch (e) {}
        }
      }
    };

    const pickupCategoriesFromSearchPage = () => {
      let htmlData = document.documentElement.innerHTML;
      let businessDetails = [];

      const searchString = "window.APP_INITIALIZATION_STATE=";
      const codeStart = htmlData.indexOf(searchString);
      const codeEnd = htmlData.indexOf("window.APP_FLAGS", codeStart);
      const code = htmlData.substring(codeStart + searchString.length, codeEnd);

      //slice the string to remove simicolon before parsing
      const WindowInitializationState = JSON.parse(code.slice(0, -1));
      const subString = WindowInitializationState[3][2];
      if (subString && subString.length > 5) {
        const windowArray = JSON.parse(subString.substring(5));

        for (var i = 1; i < windowArray[0][1].length; i++) {
          try {
            businessDetails.push({
              "business-title": windowArray[0][1][i][14][11],
              "business-address": windowArray[0][1][i][14][2][0],
              "business-categories": windowArray[0][1][i][14][13],
            });
          } catch (e) {
            Logger.error(e);
          }
        }
      }

      return businessDetails;
    };

    const convertBusinessHtmlObjectToArray = (htmlObjectList) => {
      let businessNameObject,
        tempAddressValue = "";
      let businessDetailsArray = [];
      for (let i = 0; i < htmlObjectList.length; i++) {
        businessNameObject = htmlObjectList[i];
        try {
          tempAddressValue = businessNameObject.parentNode.parentNode.parentNode
            .getElementsByClassName("section-subtitle-extension")[0]
            .parentElement.innerText.split("  ")[1]
            .split("\n")[0];
        } catch (e) {
          tempAddressValue = "";
        }
        businessDetailsArray.push({
          "business-title": htmlObjectList[i].innerText.trim(),
          "business-address": tempAddressValue,
          "business-page-url":
            businessNameObject.parentNode.parentNode.parentNode.parentNode.parentNode.parentNode.parentNode.parentNode.querySelector(
              "a"
            ).href,
          "business-categories": "",
        });
      }
      return businessDetailsArray;
    };

    const isTwoArraySameForOrderAndBusinessDetail = (
      newBusinessArray,
      existingBusinessArray
    ) => {
      let isDifferenceFound = false;
      if (
        newBusinessArray &&
        existingBusinessArray &&
        newBusinessArray.length !== existingBusinessArray.length
      ) {
        return false;
      } else {
        for (let i = 0; i < newBusinessArray.length; i++) {
          if (
            newBusinessArray[i]["business-title"] !==
              existingBusinessArray[i]["business-title"] ||
            newBusinessArray[i]["business-address"] !==
              existingBusinessArray[i]["business-address"]
          ) {
            isDifferenceFound = true;
            break;
          }
        }
      }
      return !isDifferenceFound;
    };

    // Adding search terms to the map
    const addSearchResultCategoryToMap = () => {
      let newBusinessNameList = [];
      let listOfElementWithBusinessDetails = document.querySelectorAll(
        "div.fontBodyMedium > div > div.fontHeadlineSmall"
      );

      searchResultCheckIfLoadingSymbolThereOrAddIt(
        listOfElementWithBusinessDetails
      );
      newBusinessNameList = convertBusinessHtmlObjectToArray(
        listOfElementWithBusinessDetails
      );
      let temporaryIsFoundFlag;

      //This is for comparing array. Done progressive checking so there is less resources used.
      if (
        listOfElementWithBusinessDetails.length > 0 &&
        existingBusinessListOnPage.length > 0 &&
        isTwoArraySameForOrderAndBusinessDetail(
          newBusinessNameList,
          existingBusinessListOnPage
        )
      ) {
        return;
      } else {
        existingBusinessListOnPage = newBusinessNameList;
      }

      //This is in case the array gets too big for search.
      if (searchResultItemsArray.length > 100) {
        searchResultItemsArray = [];
      }

      for (let j = 0; j < newBusinessNameList.length; j++) {
        temporaryIsFoundFlag = false;
        for (let k = 0; k < searchResultItemsArray.length; k++) {
          if (
            searchResultItemsArray[k]["business-title"] ===
              newBusinessNameList[j]["business-title"] &&
            searchResultItemsArray[k]["business-address"] ===
              newBusinessNameList[j]["business-address"]
          ) {
            temporaryIsFoundFlag = true;
            newBusinessNameList[j]["business-categories"] =
              searchResultItemsArray[k]["business-categories"];
            break;
          }
        }
        if (temporaryIsFoundFlag) {
          displayCategoriesOnSearchResultPage(
            newBusinessNameList[j]["business-title"],
            newBusinessNameList[j]["business-categories"],
            listOfElementWithBusinessDetails
          );
        } else {
          findCategoriesFromUrlAndDisplay(
            newBusinessNameList[j],
            listOfElementWithBusinessDetails
          );
        }
      }
    };

    const findCategoriesFromUrlAndDisplay = (
      indivitualBusinessDetail,
      listOfElementWithBusinessDetails
    ) => {
      callGoogleMap(indivitualBusinessDetail["business-page-url"], (data) => {
        try {
          const onPageHTML = data;
          const processedTxt =
            onPageHTML && onPageHTML[3] ? onPageHTML[3][6] : "";
          if (processedTxt && processedTxt.length > 5) {
            const windowArray = JSON.parse(processedTxt.substring(5));
            const categories = windowArray[6][13];

            if (categories && categories.length) {
              displayCategoriesOnSearchResultPage(
                indivitualBusinessDetail["business-title"],
                categories,
                listOfElementWithBusinessDetails
              );
              indivitualBusinessDetail["business-categories"] = categories;
              searchResultItemsArray.push(indivitualBusinessDetail);
            } else {
              displayNotFoundMessageOnSearchResultPage(
                indivitualBusinessDetail["business-title"],
                listOfElementWithBusinessDetails
              );
            }
          } else {
            displayNotFoundMessageOnSearchResultPage(
              indivitualBusinessDetail["business-title"],
              listOfElementWithBusinessDetails
            );
          }
        } catch {
          displayNotFoundMessageOnSearchResultPage(
            indivitualBusinessDetail["business-title"],
            listOfElementWithBusinessDetails
          );
        }
      });
    };

    //---------------------------------------------

    (function () {
      Utility.setHeaderStyles(Utility.gmbCategoryClass);
      let url = new URL(window.location.href);

      const getAdminSectionBusinessName = () => { 
        //for unverified business
        var unverifiedBusiness = document.querySelector("section>div[data-ved]  div[role='heading']")
        if(unverifiedBusiness) {
          return unverifiedBusiness?.textContent;
        }

        const business = document.querySelector("form[action='/search'][role='search'] textarea[name='q']");
        if (business) {
          return business?.value;
        }
        return "";
      };

      //Pick value directly for categories.
      if (
        window.location.href.toString().indexOf("/maps") !== -1 ||
        window.location.href.toString().indexOf("maps.google") !== -1
      ) {
        let isTeleportSearch = document.URL.includes("gevercode");
        if (
          window.location.href.toString().indexOf("/maps/search/") != -1 &&
          !isTeleportSearch
        ) {
          searchResultItemsArray = searchResultItemsArray.concat(
            pickupCategoriesFromSearchPage()
          );
        } else {
          findBusinessRankForTeleport(FlagAndConstants.codeConstants);
        }

        setInterval(() => {
          if (
            FlagAndConstants.flag.individualPage &&
            window.location.href.toString().indexOf("/maps/place/") != -1
          ) {
            if (
              (!propertyName && getCurrentPropertyName()) ||
              (propertyName && !isSamePropertyName())
            ) {
              propertyName = getCurrentPropertyName();
              setGmbCategoryValue(loadingTxt);
              modifyPage();
            }

            if (
              document.querySelectorAll(
                "div.fontBodyMedium > div > div.fontHeadlineSmall"
              ).length > 0
            ) {
              try {
                addSearchResultCategoryToMap();
                appendLocalScanButton();
              } catch (e) {
                isContinueExecuting = true;
                document
                  .querySelectorAll("." + searchPageCategoryElementClass)
                  .forEach((e) => e.remove());
                Logger.error(e);
              }
            }
          } else if (
            FlagAndConstants.flag.searchPage &&
            isContinueExecuting &&
            window.location.href.toString().indexOf("/maps/search/") != -1 &&
            !isTeleportSearch
          ) {
            try {
              addSearchResultCategoryToMap();
              appendLocalScanButton();

            } catch (e) {
              isContinueExecuting = false;
              document
                .querySelectorAll("." + searchPageCategoryElementClass)
                .forEach((e) => e.remove());
              Logger.error(e);
            }
          }
        }, 1000);
      } else if (
        FlagAndConstants.flag.localFinder &&
        LocalFinder.isLocalFinder()
      ) {
        LocalFinder.showAuditButtonOnSearchResultPane();
        LocalFinder.attachLocalScanToLocalFinder();
        setInterval(() => {
          if (LocalFinder.validateBusiness()) {
            LocalFinder.execute();
          }
        }, 1000);
        // Condition for showing button on the google search result page below.
      } else if (
        (FlagAndConstants.flag.googleSearchResultPageBasicAudit ||
          FlagAndConstants.flag.googleSearchResultPageReviewAudit ||
          FlagAndConstants.flag.googleSearchResultPagePostAudit || 
          FlagAndConstants.flag.googleSearchResultPageLocalScan) &&
        url.searchParams.has("q") &&
        url.pathname.indexOf("/search") === 0 &&
        !url.searchParams.get("ge-mode")
      ) {
        let webElementContainingMap;
        setTimeout(() => {

          if (FlagAndConstants.flag.gmbAdminSection) {
            try {
              // Admin Section
              let isButtonInAdminQandASectionAdded = false;
              let isButtonInAdminUnpublishedQandASectionAdded = false;
              let isButtonInAdminTilesAdded = false;
              let isButtonInAdminReviewsAdded = false;
              setInterval(() => {
                // cannot end the interval as the modal can be closed and reopened without refresh
                // wrap in try catch each section to still allow other buttons to be added

                try {
                  // Google Business: Admin Section > Edit Profile
                  if (window.location.href.indexOf("mpd=editprofile/info") !== -1 || 
                    // this is for unpublished business (Not Publicly Visible) 
                    // they have different url format e.g #mpd=~10098141485292717275/editprofile/info
                    (window.location.href.indexOf("mpd=") !== -1 && window.location.href.indexOf("/editprofile/info") !== -1)) {
                    const iframeElement = document.querySelector('iframe[sandbox][src]');
                    if (iframeElement) {
                      const iframeDocument = iframeElement?.contentDocument || iframeElement?.contentWindow?.document;

                      const businessName = getAdminSectionBusinessName();
                      if(iframeDocument) {
                        const businessCategoryParent = iframeDocument.querySelector("[data-verified-origin]  main > div > div > div > div > div > div > section:nth-of-type(2) > div > div > div");
                        let adminBusinessCategories = [];
                        const businessCategoryToolButton = businessCategoryParent?.querySelector('.btn-gmb-category-tool');
                        if(businessCategoryParent && !businessCategoryToolButton) {
                          businessCategoryParent.style.setProperty("display", "flex", "important");
                          businessCategoryParent.style.setProperty("flex-direction", "row", "important");
                          businessCategoryParent.style.setProperty("justify-content", "flex-start", "important");

                          //get categories
                          if (businessCategoryParent.nextElementSibling && businessCategoryParent.nextElementSibling.querySelectorAll("ul>li").length > 0) {
                            businessCategoryParent.nextElementSibling.querySelectorAll("ul>li").forEach((li) => {
                              var span = li.querySelector('span');
                              if (span) {
                                  var category = span.textContent;
                                  adminBusinessCategories.push(category);
                              }
                            });

                            const container = document.createElement("div");
                            Utility.appendCategoryTool(container, adminBusinessCategories, FlagAndConstants.gmbSearchType.gmbEditProfileAdmin);
                            businessCategoryParent.appendChild(container);
                          }
                        }
                        
                        // Area: edit business categories
                        const editBusinessCategoryParent = iframeDocument.querySelector("[data-verified-origin]  main > div > div > div > div > div > div > section:nth-of-type(2) > div")?.nextElementSibling?.querySelector('div > div > div');
                        const editBusinessGmbCategoryButton = editBusinessCategoryParent?.querySelector('.btn-gmb-category-tool');
                        if(editBusinessCategoryParent && !editBusinessGmbCategoryButton) {
                          editBusinessCategoryParent.style.setProperty("display", "flex", "important");
                          editBusinessCategoryParent.style.setProperty("flex-direction", "row", "important");
                          editBusinessCategoryParent.style.setProperty("justify-content", "flex-start", "important");

                          const container = document.createElement("div");
                          container.style.marginLeft = '10px';
                          Utility.appendCategoryTool(container, adminBusinessCategories, FlagAndConstants.gmbSearchType.gmbEditProfileAdmin);
                          editBusinessCategoryParent.appendChild(container);
                        }

                        // Area: edit business - add another category button
                        var addAnotherCategoryParent = iframeDocument.querySelector("[data-verified-origin]  main > div > div > div > div > div > div > section:nth-of-type(2) > div")
                        ?.nextElementSibling?.querySelector('div > div > div')
                        ?.nextElementSibling?.querySelector('section > div')
                        ?.nextElementSibling?.querySelector('div');
                        var addAnotherCategory = addAnotherCategoryParent?.querySelector('div > div:nth-of-type(2)')?.nextElementSibling;
                        const addAnotherCategoryButton = addAnotherCategoryParent?.querySelector('.btn-gmb-category-tool');
                        if(addAnotherCategory && !addAnotherCategoryButton) {

                          // get categories
                          var categories = [];
                          var categoryElements = addAnotherCategoryParent?.querySelector('div > div:nth-of-type(2)')?.querySelectorAll("label > input");
                          categoryElements?.forEach((input) => {
                            const categoryText = input?.value;
                            if(categoryText) {
                              categories.push(categoryText);
                            }
                          });

                          const container = document.createElement("div");
                          Utility.appendCategoryTool(container, categories, FlagAndConstants.gmbSearchType.gmbEditProfileAdmin);
                          addAnotherCategory.style.display = "inline-block";
                          container.style.display =  "inline-block";
                          addAnotherCategory.parentNode.insertBefore(container, addAnotherCategory.nextSibling);
                        }

                        // Area: description
                        const generateDescriptionParent = iframeDocument.querySelector("[data-verified-origin]  main > div > div > div > div > div > div > section:nth-of-type(3) > div > div > div");
                        const generateDescriptionButton = generateDescriptionParent?.querySelector('.btn-gmb-generate-desc');
                        if(generateDescriptionParent && !generateDescriptionButton) {
                          generateDescriptionParent.style.setProperty("display", "flex", "important");
                          generateDescriptionParent.style.setProperty("flex-direction", "row", "important");
                          generateDescriptionParent.style.setProperty("justify-content", "flex-start", "important");

                          const generateDescriptionButtonContainer = document.createElement("div");
                          Utility.appendGenerateDescriptionButton(generateDescriptionButtonContainer, businessName);
                          generateDescriptionParent.appendChild(generateDescriptionButtonContainer);
                        }
                      }
                    }
                  }
                } catch {}

                try {
                  // Google Business: Admin Section > Add Update
                  const addUpdateUrlChecks = [
                    "mpd=promote/updates/menu",
                    "mpd=promote/updates/add",
                    "mpd=promote/offers/add",
                    "mpd=promote/events/add",
                  ];

                  if (addUpdateUrlChecks.some(value => window.location.href.indexOf(value) !== -1)) 
                  {
                    const iframeElement = document.querySelector('iframe[sandbox][src]');
                    const businessName = getAdminSectionBusinessName();
                    if (iframeElement) {
                      const iframeDocument = iframeElement?.contentDocument || iframeElement?.contentWindow?.document;

                      if(iframeDocument) {
                        const addUpdateHeaderParent =  iframeDocument.querySelector("header > h1");
                        const addUpdateHeaderGmbPostButton = addUpdateHeaderParent?.querySelector('.btn-gmb-generate-post');
                        if(addUpdateHeaderParent && !addUpdateHeaderGmbPostButton) {
                          addUpdateHeaderParent.style.setProperty("display", "flex", "important");
                          addUpdateHeaderParent.style.setProperty("flex-direction", "row", "important");
                          addUpdateHeaderParent.style.setProperty("justify-content", "flex-start", "important");

                          const generatePostButtonContainer = document.createElement("div");
                          Utility.appendGeneratePostButton(generatePostButtonContainer, businessName);
                          addUpdateHeaderParent.appendChild(generatePostButtonContainer);
                        }
                      }
                    }
                  }
                } catch {}

                try {
                  // Google Business: Admin Section > Q & A
                  if (window.location.href.indexOf("lpqa=") !== -1) {
                    var qaElement = document.querySelector("span[jsslot]>div>div[id][data-async-context][data-jiis][data-async-context-required] > div>div>div>div>div>div");
                    if (qaElement && !isButtonInAdminQandASectionAdded) {
                      qaElement.style.setProperty("display", "flex", "important");
                      qaElement.style.setProperty("flex-direction", "row", "important");
                      qaElement.style.setProperty("justify-content", "flex-start", "important");

                      const generateQandAButtonContainer = document.createElement("div");
                      const businessName = getAdminSectionBusinessName();
                      Utility.appendGenerateQandAButton(generateQandAButtonContainer, businessName);
                      qaElement.appendChild(generateQandAButtonContainer);

                      qaElement.childNodes[0].nodeValue = Utility.truncateText(businessName, 25);

                      isButtonInAdminQandASectionAdded = true;
                    }
                  } else {
                    isButtonInAdminQandASectionAdded = false;
                  }
                } catch {}

                try {
                  // Google Business: Admin Section > Q & A - unpublished business
                  // it has different logic than the published business as this one is an iframe
                  if (window.location.href.indexOf("mpd=") !== -1 && window.location.href.indexOf("/customers/questions/unpublished") !== -1) {
                    const iframeElement = document.querySelector('iframe[sandbox][src]');

                    if (iframeElement && !isButtonInAdminUnpublishedQandASectionAdded) {
                      const iframeDocument = iframeElement?.contentDocument || iframeElement?.contentWindow?.document;

                      if(iframeDocument) {
                        const qaHeaderParent =  iframeDocument.querySelector("header > h1");
                        if(qaHeaderParent) {
                          qaHeaderParent.style.setProperty("display", "flex", "important");
                          qaHeaderParent.style.setProperty("flex-direction", "row", "important");
                          qaHeaderParent.style.setProperty("justify-content", "flex-start", "important");

                          const generateQandAButtonContainer = document.createElement("div");
                          const businessName = getAdminSectionBusinessName();
                          Utility.appendGenerateQandAButton(generateQandAButtonContainer, businessName);
                          qaHeaderParent.appendChild(generateQandAButtonContainer);

                          isButtonInAdminUnpublishedQandASectionAdded = true;
                        }
                      }
                    }
                  } else {
                    isButtonInAdminUnpublishedQandASectionAdded = false;
                  }
                } catch {}

                try {
                  // admin section tiles
                  if (window.location.href.indexOf("mat=") !== -1) {
                    const tilesParent = document.querySelector("section[data-nagarde][data-nagide][data-ved] > div > div > ul");
                    if(tilesParent && !isButtonInAdminTilesAdded) {
                      const tilesButton = tilesParent.querySelectorAll("li")[0].querySelector("button");
                      if(tilesButton) {
                        const aiToolsContainer = document.createElement("li");
                        aiToolsContainer.className = tilesParent.querySelector("li").className;
                        aiToolsContainer.style.cssText = `
                          background-color: rgb(255, 200, 185);
                          border: 2px solid rgb(204, 77, 41);
                          border-radius: 3px;
                        `;
                        
                        const businessName = getAdminSectionBusinessName();
                        Utility.appendAIToolsButton(tilesButton, aiToolsContainer, businessName);
                        tilesParent.appendChild(aiToolsContainer);
        
                        isButtonInAdminTilesAdded = true;
                      }
                    }
                  } else {
                    isButtonInAdminTilesAdded = false;
                  }
                } catch {}

                try {
                  // admin section > Read Reviews
                  if (window.location.href.indexOf("&mat=") !== -1 && 
                    (window.location.href.indexOf("lrd=") !== -1 || 
                    (window.location.href.indexOf("mpd=") !== -1 && window.location.href.indexOf("/customers/reviews") !== -1))) 
                  {
                    // v1 display of read reviews
                    const reviewSection = document.querySelector("div[data-google-review-count][data-next-page-token]");
                    if (reviewSection && !isButtonInAdminReviewsAdded) {
                      const reviews = document.querySelector("div[data-google-review-count][data-next-page-token]")?.childNodes;
                      if(reviews?.length > 0) {
                        reviews.forEach((review) => {
                          const replyContainer = review.querySelector(":scope > div:nth-child(3)");
                          if(replyContainer) {
                            const reviewText = review.querySelector("span[data-expandable-section]")?.textContent;
                            const generateReplyButtonContainer = document.createElement("div");
                            Utility.appendGenerateReplyButton(generateReplyButtonContainer, reviewText);
        
                            const buttons = replyContainer.querySelectorAll("button");
                            if (buttons.length > 1) {
                              replyContainer.querySelector("div").insertBefore(generateReplyButtonContainer, buttons[1]);
                            } else {
                              replyContainer.appendChild(generateReplyButtonContainer);
                            }
                            isButtonInAdminReviewsAdded = true;
                          }
                        });
                      }
                    }

                    // v2 display of read reviews
                    const iframeElement = document.querySelector('iframe[sandbox][src]');
                    if(iframeElement) {
                      const iframeDocument = iframeElement?.contentDocument || iframeElement?.contentWindow?.document;
                      if(iframeDocument) {
                        const reviews2 = iframeDocument.querySelectorAll("c-wiz[c-wiz] > div > article");
                        if(reviews2?.length > 0) {
                          reviews2.forEach((review) => {
                            const replyContainer2 = review.nextSibling.querySelector("button").parentElement;
                            const generateReplyButton = replyContainer2.querySelector('.btn-gmb-generate-reply');
                            if(replyContainer2 && !generateReplyButton) {
                                const reviewText = review.querySelector("span[id] > span")?.nextSibling?.textContent
                                const generateReplyButtonContainer = document.createElement("div");
                                const buttons = replyContainer2.querySelectorAll("button");
                                if (buttons.length > 1) {
                                  // this is to check the style of the review "reply" button, in order to determine 
                                  // which "generate reply" button to show
                                  const buttonVersion = buttons[0].querySelector("svg") ? 1 : 2;
                                  Utility.appendGenerateReplyButton(generateReplyButtonContainer, reviewText, buttonVersion);
                                  replyContainer2.insertBefore(generateReplyButtonContainer, buttons[1]);
                                } else {
                                  // appear in "all", "replied", "unreplied" tabs
                                  Utility.appendGenerateReplyButton(generateReplyButtonContainer, reviewText);
                                  replyContainer2.appendChild(generateReplyButtonContainer);
                                }
                                isButtonInAdminReviewsAdded = true;
                            }
                          });
                        }

                        // "reply to reviews" area
                        const replyToReview = iframeDocument.querySelector('c-wiz > div > div > article');
                        const replyToReviewParent = iframeDocument.querySelector('c-wiz > div > div > article')?.parentElement;
                        const generateReplyButton = replyToReviewParent?.querySelector('.btn-gmb-generate-reply');
                        if(replyToReview && !generateReplyButton) {
                          const replyToReviewTxt = replyToReview.querySelector("span[id] > span")?.nextElementSibling?.innerText;
                          const replyContainer = replyToReviewParent.querySelector("section > div").nextElementSibling;
                          const buttons = replyContainer.querySelectorAll("button");

                          const generateReplyButtonContainer = document.createElement("div");
                          Utility.appendGenerateReplyButton(generateReplyButtonContainer, replyToReviewTxt, 3);
                          replyContainer.insertBefore(generateReplyButtonContainer, buttons[1]);
                        }

                        // modal header 
                        const readReviewsHeaderParent =  iframeDocument.querySelector("header > h1");
                        const readReviewsHeaderGmbReplyButton = readReviewsHeaderParent?.querySelector('.btn-gmb-generate-reply');
                        if(readReviewsHeaderParent && !readReviewsHeaderGmbReplyButton) {
                          readReviewsHeaderParent.style.setProperty("display", "flex", "important");
                          readReviewsHeaderParent.style.setProperty("flex-direction", "row", "important");
                          readReviewsHeaderParent.style.setProperty("justify-content", "flex-start", "important");

                          const generateReplyButtonContainer = document.createElement("div");
                          Utility.appendGenerateReplyButton(generateReplyButtonContainer, '', 4);
                          readReviewsHeaderParent.appendChild(generateReplyButtonContainer);
                        }
                      }
                    }
                  } else {
                    isButtonInAdminReviewsAdded = false;
                  }
                } catch {}

                try {
                  // admin section > edit services
                  if (window.location.href.indexOf("mpd=") !== -1 && window.location.href.indexOf("/editprofile/services") !== -1) {
                    const iframeElement = document.querySelector('iframe[sandbox][src]');
                    if (iframeElement) {
                      const iframeDocument = iframeElement?.contentDocument || iframeElement?.contentWindow?.document;
                      
                      if(iframeDocument) {
                        const editServicesParent =  iframeDocument.querySelector("header > h1");
                        const editServicesGmbServiceButton = editServicesParent?.querySelector('.btn-gmb-category-tool');
                        if(editServicesParent && !editServicesGmbServiceButton) {
                          editServicesParent.style.setProperty("display", "flex", "important");
                          editServicesParent.style.setProperty("flex-direction", "row", "important");
                          editServicesParent.style.setProperty("justify-content", "flex-start", "important");

                          // const categorySections = iframeDocument.querySelectorAll("main > div > div > div > div > section");
                          const categorySections = iframeDocument.querySelectorAll("main section");
                          if (categorySections) {
                            let categories = [];
                            categorySections.forEach((section) => {
                              const categoryText = section.querySelector("span[id][role]")?.textContent
                              if (categoryText) {
                                categories.push(categoryText);
                              }
                            });

                            const container = document.createElement("div");
                            Utility.appendCategoryTool(container, categories, FlagAndConstants.gmbSearchType.gmbEditServicesAdmin);
                            editServicesParent.appendChild(container);
                          }
                        }
                      }
                    }
                  }
                } catch {}

              }, 3000);
            } catch {}
          } 

          try {
            if (
              (webElementContainingMap =
                LocalFinder.loopAndSeeIfMapSectionAnywhereOnPage())
            ) {
              //assigning value
              LocalFinder.displayButtonOnMapSection(webElementContainingMap);
            } else if (
              (webElementContainingMap =
                LocalFinder.loopAndSeeIfMapSectionAnywhereOnPage_v4())
            ) {
              LocalFinder.displayButtonOnMapSection_v3(webElementContainingMap);
            } else if (
              (webElementContainingMap =
                LocalFinder.loopAndSeeIfMapSectionAnywhereOnPage_v5())
            ) {
              LocalFinder.displayButtonOnMapSection_v3(webElementContainingMap);
            } 
            else if (
              (webElementContainingMap =
                LocalFinder.loopAndSeeIfMapSectionAnywhereOnPage_v2())
            ) {
              LocalFinder.displayButtonOnMapSection_v2(webElementContainingMap);
            } else if (
              (webElementContainingMap =
                LocalFinder.loopAndSeeIfMapSectionAnywhereOnPage_v3())
            ) {
              LocalFinder.displayButtonOnMapSection_v3(webElementContainingMap);
            } else if (
              (webElementContainingMap =
                LocalFinder.loopAndSeeIfMapSectionAnywhereOnPage_v6())
            ) {
              LocalFinder.displayButtonOnMapSection_v3(webElementContainingMap);
              attachLocalScanToGoogleSearchResult(webElementContainingMap);
            }
          } catch (e) {}
        }, 1000);
      } else if (
        url.pathname.indexOf("/search") === 0 &&
        url.searchParams.get("ge-mode") ===
          FlagAndConstants.geMode["scrapping-review-page"]["ge-mode"]
      ) {
        try {
          if (
            url.searchParams.get("unique-code") &&
            url.searchParams.get("number-of-reviews")
          ) {
            Utility.createFullPageLoader(
              "GMB Everywhere is processing.<br>Stay on this tab to continue processing."
            );
            // find review audit modal
            const intervalTime = 500;
            const maxAttempts = 40;
            Utility.checkIfElementExists(
              ".review-dialog-body",
              intervalTime,
              maxAttempts,
              () => {
                RetrieveReviews.ScrollThroughReviewsAndForward();
              },
              () => {
                Utility.createFullPageLoader(
                  "An error occured. Please try again.", true
                );

                Utility.forwardToErrorPage({
                  auditType: 'review-audit',
                  errorType: 'eu-dma',
                  newTab: false
                });

                return;
              }
            );
          } else {
            Utility.createFullPageLoader(
              "An error occured. Please try again.", true
            );
          }
        } catch (e) {
          Utility.createFullPageLoader(
            "An error occured. Please try again.", true
          );
        }
      } else if (
        url.pathname.indexOf("/search") === 0 &&
        url.searchParams.get("ge-mode") ===
          FlagAndConstants.geMode["scrapping-post-page"]["ge-mode"]
      ) {
        try {
          RetrievePost.forwardToPostPage();
        } catch (e) {
          Utility.createFullPageLoader(
            "An error occured. Please try again.", true
          );
        }
      }
    })();
  } catch (e) {
    isContinueExecuting = false;
    document
      .querySelectorAll("." + searchPageCategoryElementClass)
      .forEach((e) => e.remove());
    Logger.error(e);
  }
})();
