import { displayFinalReviewarray } from "./display-final-review";
import { codeConstants, REVIEW_AUDIT_MODAL_VERSION } from "../feature-flag-and-constants";
import { getReviewAuditModalVersion, forwardToErrorPage } from "../utility"

let reviewAuditModalVersion;

const ScrollThroughReviewsAndForward = () => {
  reviewAuditModalVersion = getReviewAuditModalVersion();

  var url = new URL(window.location.href);
  var reviewParam = url.searchParams.get("number-of-reviews");
  // Remove non-numeric characters from the reviewParam
  // This is to safeguard against any non-numeric characters in the number-of-reviews param in the URL 
  // if the URL has no fragment since we added an 'n' to the end of the URL 
  var numberOfReviews = reviewParam ? parseInt(reviewParam.replace(/[^0-9]/g, '')) : 0;

  const maximumReviewAllowed =
    codeConstants["audit-dashboard"][
      "maximum-number-of-reviews-needed-scraping"
    ];
  setTimeout(function () {

    if(reviewAuditModalVersion === REVIEW_AUDIT_MODAL_VERSION.v1) {
      document.querySelector('[data-sort-id="newestFirst"]').click()
    } else {
      document.querySelector('g-scrolling-carousel[jsshadow] div[data-sort="2"]').click();
    }

    scrollWithTimeLag(numberOfReviews, maximumReviewAllowed, 0, 0);
  }, 2000);
};

let scrollReviewsSection = () => {
  let element;
  let hasValidElement = false;
  if(reviewAuditModalVersion === REVIEW_AUDIT_MODAL_VERSION.v1) {
    element = document.getElementsByClassName("review-dialog-list");
    if(element.length > 0) {
      element = element[0];
      hasValidElement = true;
    }
  }
  else if (reviewAuditModalVersion === REVIEW_AUDIT_MODAL_VERSION.v2) {
    element = document.querySelectorAll(".review-dialog > c-wiz[data-feature-id] > div");
    if(element.length > 2) {
      element = element[2];
      hasValidElement = true;
    }

    element = document.querySelectorAll("div[role='dialog'] > div > c-wiz[data-feature-id] > div");
    if(element.length > 2) {
      element = element[2];
      hasValidElement = true;
    }
  }

  if(hasValidElement) {
    element.scrollTo(0, element.scrollHeight);
  } else {
    forwardToErrorPage({
      auditType: "review-audit",
      error: new Error("No valid element found to scroll to"),
      newTab: false
    });

    return;
  }
};

let scrollWithTimeLag = (
  totalNumberOfReview,
  maximumReviewAllowed,
  previousNumberOfReviewsOnPage,
  retryCount
) => {
  setTimeout(function () {
    let presentNumberOfReviewsOnPage;
    if(reviewAuditModalVersion === REVIEW_AUDIT_MODAL_VERSION.v1) {
      presentNumberOfReviewsOnPage = document.getElementsByClassName(
        "gws-localreviews__google-review"
        ).length;
    } else {
      presentNumberOfReviewsOnPage = document.querySelectorAll('div[jsdata] > div[data-ved] div[data-id][data-hveid]').length;
    }
   
    scrollReviewsSection();

    if (
      !(
        presentNumberOfReviewsOnPage == totalNumberOfReview ||
        presentNumberOfReviewsOnPage >= maximumReviewAllowed ||
        retryCount > 10
      )
    ) {
      
      if (previousNumberOfReviewsOnPage == presentNumberOfReviewsOnPage && !document.hidden) {
        retryCount++;
      }
      else {
        retryCount = 0;
      }

      previousNumberOfReviewsOnPage = presentNumberOfReviewsOnPage;

      scrollReviewsSection();

      scrollWithTimeLag(
        totalNumberOfReview,
        maximumReviewAllowed,
        previousNumberOfReviewsOnPage,
        retryCount
      );

    } else {
      displayFinalReviewarray();
    }
  }, 600);
};

export { ScrollThroughReviewsAndForward };
