import { codeConstants, flag } from "../feature-flag-and-constants";
import { apiPostRequest, createFullPageLoader, createGuid, extractPlaceIdFromHref, forwardToErrorPage } from "../utility";


const auditType = "local-scan";

// increased from 20 to 22 to account for the ads
// they will be filtered in the teleport page
const maxBusinessCount = 22;

// limit the business rank to 20
const businessRankLimit = 20;

// already reached the end of the list, as it could be due to a connection issue.
const sameResultScrollCounterLimit = 3;

const getCurrentDomainHostName = () => window.location.hostname;

// get businesses including ads
// ads are filtered later on
const getBusinesses = function (elements) {
    var newBusinessList = Array.from(elements)
        .slice(2)
        .filter((businessSection) =>
            businessSection.querySelector("div>div>a")?.href?.includes("/maps/place")
        );

    newBusinessList.forEach((businessSection) => {
        try {
            // TODO: Fix bug on determing ads, if ads has no banner with (visit site)
            businessSection.ads = !!isBusinessAds(businessSection) ? 1 : 0
        } catch {
            // Let all businesses through in case of an error
            businessSection.ads = 0;
        }
    });

    return newBusinessList;
}

const isBusinessAds = function (businessSection) {
    let isAds = false;
    let adSectionElements = businessSection.querySelector(
        "div>div:nth-child(2)>div>div"
    )?.children;
    if (adSectionElements) {
        isAds =
            adSectionElements.length === 4 &&
            adSectionElements[0].tagName === "SPAN" &&
            adSectionElements[1].tagName === "SPAN" &&
            adSectionElements[2].tagName === "BUTTON";
    }

    let adSectionElementsV2 = businessSection.querySelector(
        "div>div:nth-child(2)>div>div>div:nth-child(2)>div>div:nth-child(3)>div>span"
    )?.innerText;
    if (adSectionElementsV2 && isAds === false) {
        isAds = true;
    }

    const isAdsV2 = isBusinessContainsAdBannerV2(businessSection);

    // this condition kinda overrides the other ads conditions 
    // as this three dot menu button is showing for sponsored businesses even with or without ads banner
    const isAdsV3 = isBusinessContainsSponsoredThreeDotMenuButton(businessSection);

    return isAds || isAdsV2 || isAdsV3;
}

const isBusinessContainsAdBannerV2 = (businessSection) => {
    const anchorElements = businessSection.querySelectorAll('a');
    let found = false;

    anchorElements.forEach((element) => {
        const href = element.getAttribute('href');
        if (href && href.includes('/aclk?')) {
            found = true;
            return;
        }
    });

    return found;
}

const isBusinessContainsSponsoredThreeDotMenuButton = (businessSection) => {
    var buttonExists = businessSection?.querySelector("div>h1>span>span>button[aria-haspopup='true']");
    return !!buttonExists;
}

const onFinishedFindingBusinesses = () => {
    return new Promise((resolve) => {
        let businessesDetails = [];

        try {
            // results are in order, it servers as their rank
            const businessList = getBusinesses(getBusinessSectionElement().children);
            businessList.forEach((businessSection, index) => {
                // skips ads and when it reaches the max business count
                if (index > maxBusinessCount - 1 || businessSection.ads) return;

                try {
                    let businessDetail = {};
                    if (businessSection.querySelector("div>a")) {
                        const extractPlaceId = () => {
                            let placeId = "";

                            const placeIdHref = businessSection.querySelector("div>div>a")?.href;
                            if (placeIdHref) {
                                placeId = extractPlaceIdFromHref(placeIdHref);
                            }

                            if (!placeId) {
                                const anchorTags = businessSection.querySelectorAll("div a[href]");

                                if (anchorTags?.length > 0) {
                                    const hrefs = Array.from(anchorTags).map((a) => a.href);
                                    const placeIdHref = hrefs?.filter((href) =>
                                        extractPlaceIdFromHref(href)
                                    )?.[0];

                                    if (placeIdHref) {
                                        placeId = extractPlaceIdFromHref(placeIdHref);
                                    }
                                }
                            }

                            return placeId;
                        }

                        const extractBusinessName = () => {
                            let businessName = businessSection
                                .querySelector(
                                    "div>div:nth-child(4)>div>div>div>div:nth-child(2)>div>:nth-child(3)"
                                )
                                ?.innerText?.trim();
                            if (!businessName) {
                                businessName = businessSection
                                    .querySelector(".fontHeadlineSmall")
                                    ?.innerText?.trim();
                            }
                            if (!businessName) {
                                businessName = businessSection.querySelector('div>div:nth-child(4)')?.innerText?.trim();
                            }

                            return businessName;
                        }

                        businessDetail.placeId = extractPlaceId();
                        businessDetail.businessName = extractBusinessName();
                        businessDetail.exactLink = businessSection.querySelector("div>div>a")?.href;
                        businessesDetails.push(businessDetail);
                    }

                } catch { }
            });

            let bussinessRankList = businessesDetails
                .slice(0, businessRankLimit)
                .map(
                    (businessDetail, index) => {
                        let business = {
                            businessName: businessDetail.businessName,
                            pid: businessDetail.placeId,
                            businessUrl: businessDetail.placeId ?
                                `https://${getCurrentDomainHostName()}/maps/place/?q=place_id:${businessDetail.placeId}` : businessDetail.exactLink,
                            businessRank: index + 1
                        };

                        return business;
                    }
                );

            // iterate each business then call function AuditInformationLogic but with delay of 250m
            const businessesRankDetailsPromises = bussinessRankList.map((business, index) => {
                return new Promise((resolve) => {
                    setTimeout(() => {
                        let url = business.businessUrl;
                        getBusinessDetails(url)
                            .then((windowArray) => {
                                resolve({
                                    rawdata: windowArray,
                                    businessName: business.businessName,
                                    rank: business.businessRank
                                });
                            })
                            .catch((_) => {
                                resolve({
                                    rawdata: [],
                                    businessName: business.businessName,
                                    rank: business.businessRank
                                });
                            })
                    }, index * 250);
                })
            });

            Promise.all(businessesRankDetailsPromises).then((results) => {
                resolve(results);
            });
        } catch (e) {
            onError(e);
            return;
        }
    })
}


const getBusinessDetails = (businessUrl) => {
    return new Promise((resolve, reject) => {
        getWindowInitializationStateFromGoogleMap(businessUrl)
            .then((data) => {
                try {
                    const processedTxt = data && data[3] ? data[3][6] : "";
                    if (!processedTxt || processedTxt.length <= 5) {
                        throw new Error('Invalid processed text data');
                    }

                    const windowArray = JSON.parse(processedTxt.substring(5));
                    if (!windowArray || !windowArray[6]) {
                        throw new Error('Invalid window array data');
                    }

                    const dataArray = windowArray[6];
                    resolve(dataArray);
                } catch (e) {
                    reject(e);
                }
            })
            .catch((e) => {
                reject(e);
            });
    });
}

const scrollToBottom = (container) => {
    container.scrollTo(0, container.scrollHeight);
}

const scrollToTop = (container) => {
    container.scrollTo(0, 0);
}

const isCorrectBusinessSection = (businessSection) => businessSection?.matches('[role="feed"]') ?? false;

const getBusinessSectionElement = () => {
    const businessSectionV1 = document.querySelector("#pane>div>div>div>div>div>div");
    if (isCorrectBusinessSection(businessSectionV1)) return businessSectionV1;

    const businessSectionV2 = document.querySelector("#pane+div>div>div>div>div:nth-child(2)>div>div>div>div>div>div");
    if (isCorrectBusinessSection(businessSectionV2)) return businessSectionV2;

    // for hotels
    const businessSectionV3 = document.querySelector("#pane+div>div>div>div>div:nth-child(2)>div>div>div>div>div>div")?.parentElement?.nextElementSibling?.firstChild;
    if (isCorrectBusinessSection(businessSectionV3)) return businessSectionV3;

    return null;
}

const scrollToFindBusinesses = () => {
    return new Promise((resolve) => {
        try {
            const businessSectionElement = getBusinessSectionElement();

            if (!businessSectionElement) {
                onError(new Error("businessSectionElement not found!"));
                return;
            }

            const scrollLoopWithTimeLag = (
                previousNumberOfBusinessCount,
                sameResultScrollCounter
            ) => {
                setTimeout(() => {
                    try {
                        let container = getBusinessSectionElement();
                        let presentNumberOfBusinessesOnPage = getBusinesses(
                            container.children
                        ).length;

                        if (presentNumberOfBusinessesOnPage === previousNumberOfBusinessCount) {
                            sameResultScrollCounter++;
                        }

                        if (presentNumberOfBusinessesOnPage >= maxBusinessCount || sameResultScrollCounter > sameResultScrollCounterLimit) {
                            scrollToTop(container);

                            // done scrolling, resolve the promise
                            resolve();
                        } else {
                            scrollToBottom(container);
                            scrollLoopWithTimeLag(presentNumberOfBusinessesOnPage, sameResultScrollCounter);
                        }
                    } catch (e) {
                        onError(e);
                        return
                    }
                }, 1000);
            }

            scrollLoopWithTimeLag(0, 0);
        } catch (e) {
            onError(e);
            return;
        }
    })
}

const onError = (e) => {
    hideGMBOverlay();
    forwardToErrorPage({
        auditType: auditType,
        error: e,
        newTab: true,
    })
}

const hideGMBOverlay = () => {
    document.getElementById("fullPageGreySection")?.remove();
}

const findBusinessesAndAnalyze = () => {
    return new Promise((resolve, reject) => {
        scrollToFindBusinesses().then(() => {
            onFinishedFindingBusinesses().then((results) => {

                const uniqueGUIDKey = createGuid();
                const dashboardUrl = codeConstants["local-scan"]["dashboard-url"];
                const sendDataApiUrl = codeConstants["local-scan"]["send-data-api-url"];

                const url = new URL(dashboardUrl);
                url.searchParams.set("data-unique-code", uniqueGUIDKey);

                let hasNavigated = false;

                // Set a timeout to force navigation after 3 seconds
                const timeoutId = setTimeout(() => {
                    if (!hasNavigated) {
                        hasNavigated = true;
                        hideGMBOverlay();
                        window.open(url.toString(), '_blank');
                    }
                }, 3000);

                const data = {
                    "url": window.location.href,
                    "searchTerm": document.querySelector("#searchboxinput")?.value,
                    "rawData": results,
                }

                apiPostRequest(sendDataApiUrl, {
                    "unique_code": uniqueGUIDKey,
                    "data": JSON.stringify(data),
                    "feature_type": auditType
                }).then((_) => {
                    clearTimeout(timeoutId);
                    if (!hasNavigated) {
                        hasNavigated = true;
                        hideGMBOverlay();
                        window.open(url.toString(), '_blank');
                    }

                    return resolve();
                }).catch((e) => {
                    clearTimeout(timeoutId);
                    onError(e);

                    return reject(e);
                });
            });
        });
    });
}

const createLocalScanButtonForMapPage = () => {
    const onClick = () => {
        createFullPageLoader("GMB Everywhere is processing.<br>Stay on this tab to continue processing.");
        findBusinessesAndAnalyze();
    }

    const localScanButton = createLocalScanButton(onClick);

    return localScanButton;
}

const createLocalScanButton = (onClick) => {
    const button = document.createElement("button");
    button.classList.add("btn-local-scan");
    button.style.cssText = `
      border: 3px solid rgb(204, 77, 41); 
      background-color: rgb(255, 200, 185);
      color: rgb(0, 0, 0); 
      font-size: 12px; 
      padding: 5px 5px; 
      border-radius: 5px; 
      cursor: pointer; 
      font-weight: 600;
      z-index: 10;
    `;

    button.textContent = " Local Scan"

    button.onclick = (event) => {
        event.stopPropagation();
        onClick();
    }

    return button;
}

const appendLocalScanButton = () => {
    if (flag.mapSearchPageLocalScan) {
        const buttonExists = document.querySelector(".btn-local-scan");
        if (!buttonExists) {

            // check if the business section is a hotel type business
            // this is the same selector used in businessSectionV3
            const hotelTypeBusinessSection = document.querySelector("#pane+div>div>div>div>div:nth-child(2)>div>div>div>div>div>div")?.parentElement?.nextElementSibling?.firstChild;
            if (isCorrectBusinessSection(hotelTypeBusinessSection)) {
                const container = document.querySelector("div[data-callout-id]")?.parentElement?.parentElement?.parentElement?.nextElementSibling?.firstChild;
                if (container) {
                    // adjust the button design for hotel type businesses as it's always have the "sort by" button
                    var localScanButton = createLocalScanButtonForMapPage();
                    localScanButton.textContent = "Local Scan";
                    localScanButton.style.padding = "5px 2px"
                    
                    // adjusted margin for the button to fit in the layout
                    container.parentElement.style.marginLeft = '0';
                    container.insertBefore(localScanButton, container.firstChild);
                }
                return;
            }

            // for other types of businesses
            const container = document.querySelector("div[data-callout-id]")?.parentElement?.parentElement?.parentElement.nextElementSibling;
            if (container) {
                var localScanButton = createLocalScanButtonForMapPage();

                // adjust the button if there's a "sort by" button
                const sortByButton = container.querySelector("button[aria-haspopup='menu']");
                if (sortByButton) {
                    localScanButton.textContent = "Local Scan";
                    localScanButton.style.padding = "5px 2px"
                } 

                container.insertBefore(localScanButton, container.firstChild);
            }
        }
    }
}

const getWindowInitializationStateFromGoogleMap = (url) => {
    return new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest();

        xhr.onreadystatechange = () => {
            if (xhr.readyState === XMLHttpRequest.DONE) {
                const status = xhr.status;
                if (status === 0 || (status >= 200 && status < 400)) {
                    try {
                        const searchString = "window.APP_INITIALIZATION_STATE=";
                        const codeStart = xhr.response.indexOf(searchString);
                        const codeEnd = xhr.response.indexOf("window.APP_FLAGS", codeStart);
                        const code = xhr.response.substring(
                            codeStart + searchString.length,
                            codeEnd
                        );

                        // Remove the trailing semicolon before parsing
                        const WindowInitializationState = JSON.parse(code.slice(0, -1));
                        resolve(WindowInitializationState);
                    } catch (e) {
                        reject(e); // Reject if JSON parsing or extraction fails
                    }
                } else {
                    reject(new Error(`Request failed with status: ${status}`));
                }
            }
        };

        xhr.onerror = () => {
            reject(new Error("Network error or request failed"));
        };

        xhr.open("GET", url);
        xhr.send();
    });
};

const processAndAnalyzeBusinesses = (businesses) => {
    createFullPageLoader("GMB Everywhere is processing.<br>Stay on this tab to continue processing.");
    const businessesRankDetailsPromises = businesses.map((business, index) => {
        return new Promise((resolve) => {
            setTimeout(() => {
                let url = business.businessUrl;
                getBusinessDetails(url)
                    .then((windowArray) => {
                        resolve({
                            rawdata: windowArray,
                            businessName: business.businessName,
                            rank: business.rank
                        });
                    })
                    .catch((_) => {
                        resolve({
                            rawdata: [],
                            businessName: business.businessName,
                            rank: business.rank
                        });
                    })
            }, index * 250);
        })
    });

    Promise.all(businessesRankDetailsPromises).then((results) => {

        const uniqueGUIDKey = createGuid();
        const dashboardUrl = codeConstants["local-scan"]["dashboard-url"];
        const sendDataApiUrl = codeConstants["local-scan"]["send-data-api-url"];

        const url = new URL(dashboardUrl);
        url.searchParams.set("data-unique-code", uniqueGUIDKey);

        let hasNavigated = false;

        // Set a timeout to force navigation after 3 seconds
        const timeoutId = setTimeout(() => {
            if (!hasNavigated) {
                hasNavigated = true;
                hideGMBOverlay();
                window.open(url.toString(), '_blank');
            }
        }, 3000);

        const data = {
            "url": window.location.href,
            "searchTerm": document.querySelector("#searchboxinput")?.value,
            "rawData": results,
        }

        apiPostRequest(sendDataApiUrl, {
            "unique_code": uniqueGUIDKey,
            "data": JSON.stringify(data),
            "feature_type": auditType
        }).then((_) => {
            clearTimeout(timeoutId);
            if (!hasNavigated) {
                hasNavigated = true;
                hideGMBOverlay();
                window.open(url.toString(), '_blank');
            }

        }).catch((e) => {
            clearTimeout(timeoutId);
            onError(e);
        });
    });
}

export { appendLocalScanButton, createLocalScanButton, onError, processAndAnalyzeBusinesses };

