import { parse, format, addDays, addHours, addWeeks, addMinutes } from 'date-fns';

import { codeConstants } from "../feature-flag-and-constants";

import { countNumberOfTimeValueExistInArray, returnUniqueValuesInArray, getUrlAppendUserInformationAndForward, createFullPageLoader } from '../utility';


let isPageContainsPost = () => {
  let htmlData = document.documentElement.innerHTML;
  if (htmlData.indexOf("Latest from the owner") != -1 || htmlData.indexOf("View previous updates on Google") != -1) {
    return true;
  }
  return false;
}


let scrollPostWithTimeLag = (maximumPostToBeScrapped, presentNumberOfPostOnPageClicker = 0) => {
  setTimeout(function () {
    let postSection = document.querySelectorAll("[data-show-pinned-post]")[0];
    
    postSection.firstChild.scrollTo(0, postSection.firstChild.scrollHeight);
    let calculatedNumberOfPostsOnPage = document.querySelectorAll("article[data-post-id][data-feature-id]:not([data-post-type])").length;

    if ((calculatedNumberOfPostsOnPage < maximumPostToBeScrapped) && (presentNumberOfPostOnPageClicker !== calculatedNumberOfPostsOnPage)) {
      scrollPostWithTimeLag(maximumPostToBeScrapped, calculatedNumberOfPostsOnPage);
    } else {
      displayFinalPostArray();
    }
    // console.log(calculatedNumberOfPostsOnPage);
  }, 2000);
}

let findMoreButtonsAndClickThem = (indivitualPostNodeSet) => {
  let nodesWithMoreTextArray = [];
  let spanElement;
  for (let i = 0; i < indivitualPostNodeSet.length; i++) {

    spanElement = indivitualPostNodeSet[i].querySelectorAll("span");
    for (let j = 0; j < spanElement.length; j++) {
      if (spanElement[j].textContent.toLowerCase() === "more") {
        nodesWithMoreTextArray.push(spanElement[j]);
      }
    }
  }

  nodesWithMoreTextArray.forEach(node => node.click());
}

let getFinalPostValuesAndForward = (postValues, isPostExistOnPage) => {
  chrome.storage.local.get(['ge-business-details-key'], function (result) {
    var url = new URL(window.location.href);
    var uniqueGUIDKey = url.searchParams.get("unique-code");
    writePostToValueAndForward(result['ge-business-details-key'], postValues, uniqueGUIDKey, isPostExistOnPage);

  });
}

let writePostToValueAndForward = (resultOfStorage, finalPostArrayList, uniqueGUIDKey, isPostThereInPage) => {

  for (let i = resultOfStorage.length - 1; i >= 0; i--) {
    // URL fragment (#) truncates the last unique-code character, so we compare only first 30 characters
    if (uniqueGUIDKey && resultOfStorage[i]["key"].substring(0, 30) === uniqueGUIDKey.substring(0, 30)) {

      // let orderedREviewTimeAndCounter = getReviewCountPerDateFromReviewList(finalPostArrayList);

      // let { graphXAxis, graphYAxis } = cleanUpXYGraphData(orderedREviewTimeAndCounter, resultOfStorage[i], finalPostArrayList);
      // resultOfStorage[i]["value"]["xy-axis-Values"] = { "graph-x": graphXAxis, "graph-y": graphYAxis };
      // let calculatedReviewValues = getReviewValues(finalPostArrayList);
      resultOfStorage[i]["value"]["post-details"] = finalPostArrayList;



      let postAuditUrl = codeConstants['audit-dashboard']['post-audit-url'];
      getUrlAppendUserInformationAndForward(postAuditUrl + "?ge-mode=business-details&audit-type=post&unique-code=" +  encodeURIComponent(uniqueGUIDKey) + "&isPostExistForBusiness=" + ((isPostThereInPage)?"true": "false") + "&dashboard-data=" + encodeURIComponent(JSON.stringify(resultOfStorage[i]["value"])), true);


    }

  }
}

let convertPostDateToActualDate = (postDateAsString) => {
  let returnDate = new Date();
  const timeStringAsArray = postDateAsString.split(" ");

  if (postDateAsString.toLowerCase().indexOf("hour") !== -1) {
    if (timeStringAsArray[0].indexOf("an") !== -1) {
      returnDate = addHours(returnDate, -1);
    } else {
      returnDate = addHours(returnDate, -parseInt(timeStringAsArray[0]));
    }
  } else if (postDateAsString.toLowerCase().indexOf("day") !== -1) {
    if (timeStringAsArray[0].indexOf("a") !== -1) {
      returnDate = addDays(returnDate, -1);

    } else {
      returnDate = addDays(returnDate, -parseInt(timeStringAsArray[0]));
    }
  } else if (postDateAsString.toLowerCase().indexOf("week") !== -1) {
    if (timeStringAsArray[0].indexOf("a") !== -1) {
      returnDate = addWeeks(returnDate, -1);

    } else {
      returnDate = addWeeks(returnDate, -parseInt(timeStringAsArray[0]));
    }
  } else if (postDateAsString.toLowerCase().indexOf("min") !== -1) {
    if (timeStringAsArray[0].indexOf("a") !== -1) {
      returnDate = addMinutes(returnDate, -1);

    } else {
      returnDate = addMinutes(returnDate, -parseInt(timeStringAsArray[0]));
    }
  } else {
    returnDate = format(new Date(postDateAsString), "MM-dd-yyyy");
  }
  return returnDate;

}

let getVideoImgSectionAndTextSection = (indivitualPostNode) => {
  let returnTextAndVideoImage = {
    "text-section": null,
    "image-video-section": null
  };

  if ([3, 4].includes(indivitualPostNode.firstChild.childElementCount)) {
    /* It looks like the new version comes with only 3 children that contain media and text-section, 
       but the original condition still remains as the other customers might still have the old version */
    returnTextAndVideoImage['image-video-section'] = indivitualPostNode.firstChild.children[1];
    returnTextAndVideoImage['text-section'] = indivitualPostNode.firstChild.children[2];
  } else {
    if (indivitualPostNode.firstChild.children[1].querySelectorAll("img").length > 0 || indivitualPostNode.firstChild.children[1].querySelectorAll("video").length > 0) {
      returnTextAndVideoImage['image-video-section'] = indivitualPostNode.firstChild.children[1];
    } else {
      returnTextAndVideoImage['text-section'] = indivitualPostNode.firstChild.children[1];
    }
  }
  return returnTextAndVideoImage;

}

let displayFinalPostArray = () => {
  let indivitualPostNode = document.querySelectorAll("article[data-post-id][data-feature-id]:not([data-post-type])");
  findMoreButtonsAndClickThem(indivitualPostNode);
  setTimeout(() => {
    let postElement = [];
    let finalJSONValue;
    let totalPostCharacters = 0;
    let totalPostWords = 0;
    let totalPostWithImage = 0;
    let totalPostWithVideo = 0;
    let totalPostWithLink = 0;
    let totalNumberOfPost = indivitualPostNode.length;
    for (let i = 0; i < indivitualPostNode.length; i++) {

      let dateOfPost = convertPostDateToActualDate(indivitualPostNode[i].firstChild.children[0].children[1].children[1].innerText);

      postElement.push(format(new Date(dateOfPost), "MM-dd-yyyy"));
      let imageVideoOrTextSection = getVideoImgSectionAndTextSection(indivitualPostNode[i]);

      if (imageVideoOrTextSection['image-video-section'] && imageVideoOrTextSection['image-video-section'].querySelectorAll("img").length > 0) {
        totalPostWithImage++;

      }

      if (imageVideoOrTextSection['image-video-section'] && imageVideoOrTextSection['image-video-section'].querySelectorAll("video").length > 0) {
        totalPostWithVideo++;
      }

      if (imageVideoOrTextSection['text-section'] && imageVideoOrTextSection['text-section'].querySelectorAll("a").length>0) {
        totalPostWithLink++;
      }

      if (imageVideoOrTextSection['text-section']) {
        totalPostCharacters += imageVideoOrTextSection['text-section'].innerText.replaceAll("\n"," ").length;
        totalPostWords += imageVideoOrTextSection['text-section'].innerText.trim().split(/\s+/).length;
      }



    }
    let xAxis = returnUniqueValuesInArray(postElement).reverse();
    let yAxis = [];
    xAxis.forEach(value => yAxis.push(countNumberOfTimeValueExistInArray(value, postElement)));


    finalJSONValue = {
      "xy-axis-values": {
        "graph-x": xAxis,
        "graph-y": yAxis
      },
      "total-post-with-image": totalPostWithImage,
      "total-post-with-video": totalPostWithVideo,
      "total-post-with-links": totalPostWithLink,
      "average-post-characters": (totalPostCharacters / totalNumberOfPost).toFixed(2),
      "average-post-words": (totalPostWords / totalNumberOfPost).toFixed(2),
      "total-number-of-post": totalNumberOfPost
    };
    getFinalPostValuesAndForward(finalJSONValue, true);

  }, 2000)


  // console.log(postElement);


}

let forwardToPostPage = () => {
  let url = new URL(window.location.href);
  if (url.searchParams.get('unique-code')) {
    if (isPageContainsPost()){
      createFullPageLoader("GMB Everywhere is processing.<br>Stay on this tab to continue processing.");
      let checkPopupExist2 = setInterval(() => {

        if ( document.querySelectorAll('[data-show-pinned-post]').length > 0 ) {
          clearInterval(checkPopupExist2);
          let maximumPostSizeforScrapping = codeConstants["audit-dashboard"]["maximum-number-of-posts-needed-scraping"];
          scrollPostWithTimeLag(maximumPostSizeforScrapping);
          // RetrieveReviews.ScrollThroughReviewsAndForward();
        } 
      }, 500);
    } else {
      getFinalPostValuesAndForward(null, false);
    }
  } else {
    Utility.createFullPageLoader("An error occured. Please try again.", true);

  } 
}
          
export { forwardToPostPage };