import * as Utility from "../js/utility";
import * as Feature from "../js/feature-flag-and-constants";

const pabblyWebhook = "";

export const error = (e) => {
  try {
    if (!Feature.flag.logger) return;

    let msg = "";
    if (e) {
      msg = e.stack;
    }
    const request = `${pabblyWebhook}?url="${encodeURIComponent(
      Utility.getCurrentUrl()
    )}"&error="${encodeURIComponent(
      msg
    )}"&timestamp="${new Date().toISOString()}"`;
    var xhr = new XMLHttpRequest();
    xhr.open("GET", request, true);
    xhr.send();
  } catch {}
};
