import { createFullPageLoader, extractPlaceIdFromHref, forwardToErrorPage } from "../utility";

// SRB-9219 - get the last 5 digits of the phone,
// to avoid the inconsistency of the phone numbers */
const slicePhoneNumberLimit = 5;

// increased from 20 to 22 to account for the ads
// they will be filtered in the teleport page
const maxBusinessCount = 22;

// This is a retry limit as we cannot directly assume that the scroll attempt has 
// already reached the end of the list, as it could be due to a connection issue.
const sameResultScrollCounterLimit = 3;

let formatPhoneNumber = function (phoneNumber) {
  return phoneNumber.replace(/[^0-9]+/g, "").slice(slicePhoneNumberLimit * -1);
};

let formatAddress = function (address) {
  return address.replace(/ |,/gi, "").toLowerCase();
};

let isBusinessAds = function (businessSection) {
  let isAds = false;
  let adSectionElements = businessSection.querySelector(
    "div>div:nth-child(2)>div>div"
  )?.children;
  if (adSectionElements) {
    isAds =
      adSectionElements.length === 4 &&
      adSectionElements[0].tagName === "SPAN" &&
      adSectionElements[1].tagName === "SPAN" &&
      adSectionElements[2].tagName === "BUTTON";
  }

  let adSectionElementsV2 = businessSection.querySelector(
    "div>div:nth-child(2)>div>div>div:nth-child(2)>div>div:nth-child(3)>div>span"
  )?.innerText;
  if (adSectionElementsV2 && isAds === false) {
    isAds = true;
  }

  const isAdsV2 = isBusinessContainsAdBannerV2(businessSection);

  // this condition kinda overrides the other ads conditions 
  // as this three dot menu button is showing for sponsored businesses even with or without ads banner
  const isAdsV3 = isBusinessContainsSponsoredThreeDotMenuButton(businessSection);

  return isAds || isAdsV2 || isAdsV3;
};

const isBusinessContainsAdBannerV2 = (businessSection) => {
  const anchorElements = businessSection.querySelectorAll('a');
  let found = false;

  anchorElements.forEach((element) => {
    const href = element.getAttribute('href');
    if (href && href.includes('/aclk?')) {
      found = true;
      return;
    }
  });

  return found;
};

const isBusinessContainsSponsoredThreeDotMenuButton = (businessSection) => {
  var buttonExists = businessSection?.querySelector("div>h1>span>span>button[aria-haspopup='true']");
  return !!buttonExists;
}

// get businesses including ads
// ads are filtered in the teleport page
let getBusinesses = function (elements) {
  var newBusinessList =  Array.from(elements)
    .slice(2)
    .filter((businessSection) =>
      businessSection.querySelector("div>div>a")?.href?.includes("/maps/place")
    );

    newBusinessList.forEach((businessSection) => {
      try {
        businessSection.ads =  !!isBusinessAds(businessSection) ? 1 : 0
      } catch {
        // Let all businesses through in case of an error
        businessSection.ads = 0;
      }
    });

    return newBusinessList;
};

let findBusinessRankForTeleport = (FlagAndConstants) => {
  let teleportDashboardUrl =
    FlagAndConstants["teleport-dashboard"]["dashboard-url"];
  let googleUrl = FlagAndConstants["teleport-dashboard"]["google-url"];
  let currentPageUrl = document.URL;
  let businessRank = 1;
  let urlParams = new URLSearchParams(window.location.search);

  if (
    currentPageUrl.includes("gevercode") &&
    currentPageUrl.includes(googleUrl)
  ) {
    createFullPageLoader(
      "GMB Everywhere is processing.<br>Stay on this tab to continue processing."
    );
    document.onreadystatechange = () => {
      if (document.readyState === "complete") {
        setTimeout(function () {
          chrome.storage.local.get("searchRank", (r) => {
            if (r.searchRank && r.searchRank.length) {
              if (r.searchRank.length > 0) {
                chrome.storage.local.remove("searchRank");
              } else {
                chrome.runtime.sendMessage(
                  { method: "Dashboard" },
                  function (response) {}
                );
                return;
              }
            }

            try {
              let businessesDetails = [];
              let teleportBusinessDetail = {};
              teleportBusinessDetail.gevercode = urlParams.get("gevercode");
              teleportBusinessDetail.businessTerm = urlParams.get("businessTerm");
              teleportBusinessDetail.businessAddress =
                urlParams.get("businessAddress");
              teleportBusinessDetail.rating = parseFloat(urlParams.get("rating"));
              teleportBusinessDetail.businessName = urlParams.get("businessName");
              teleportBusinessDetail.lat = urlParams.get("lat");
              teleportBusinessDetail.lng = urlParams.get("lng");
              teleportBusinessDetail.phone = urlParams.get("phone")
                ? urlParams.get("phone").trim()
                : "";
              teleportBusinessDetail.placeId = urlParams.get("place-id");

              let businessElementSelector;
              if (document.querySelector("#pane>div>div>div>div>div>div")) {
                businessElementSelector = "#pane>div>div>div>div>div>div";
              } else if (
                document.querySelector(
                  "#pane+div>div>div>div>div:nth-child(2)>div>div>div>div>div>div"
                )
              ) {
                businessElementSelector =
                  "#pane+div>div>div>div>div:nth-child(2)>div>div>div>div>div>div";
              }
              
              if (businessElementSelector) {
                let scrollWithTimeLagTeleport = (
                  previousNumberOfBusinessCount,
                  maximumBusinessesAllowed,
                  itemsSelector,
                  callback,
                  sameResultScrollCounter = 0
                ) => {
                  setTimeout(function () {
                    let container = document.querySelector(itemsSelector);
                    let presentNumberOfBusinessesOnPage = getBusinesses(
                      container.children
                    ).length;

                    container.scrollTo(0, container.scrollHeight);

                    if (presentNumberOfBusinessesOnPage === previousNumberOfBusinessCount) {
                      sameResultScrollCounter ++;
                    }
                    
                    if (
                      !(
                        presentNumberOfBusinessesOnPage >= maximumBusinessesAllowed ||
                        sameResultScrollCounter > sameResultScrollCounterLimit
                      )
                    ) {
                      scrollWithTimeLagTeleport(
                        presentNumberOfBusinessesOnPage,
                        maximumBusinessesAllowed,
                        itemsSelector,
                        callback,
                        sameResultScrollCounter
                      );
                    } else {
                      callback();
                    }
                  }, 1000);
                };

                let onScrolledToBottomTeleport = () => {
                  getBusinesses(
                    document.querySelector(businessElementSelector).children
                  ).forEach((businessSection, index) => {
                    if (index > maxBusinessCount - 1) return;

                    let businessDetail = {};
                    try {
                      businessDetail.ads = businessSection.ads;

                      if (businessSection.querySelector("div>a")) {
                        businessDetail.title = businessSection
                          .querySelector(
                            "div>div:nth-child(4)>div>div>div>div:nth-child(2)>div>:nth-child(3)"
                          )
                          ?.innerText?.trim();
                        if (!businessDetail.title) {
                          businessDetail.title = businessSection
                            .querySelector(".fontHeadlineSmall")
                            ?.innerText?.trim();
                        }

                        if (!businessDetail.title) {
                          businessDetail.title = businessSection.querySelector('div>div:nth-child(4)')?.innerText?.trim();
                        }
    
                        businessDetail.category = businessSection
                          .querySelector(
                            "div>div:nth-child(4)>div>div>div>div:nth-child(2)>:nth-child(4)>div>span>span"
                          )
                          ?.innerText?.trim();
                          if (!businessDetail.category) {
                            businessDetail.category = businessSection
                              .querySelector(".fontHeadlineSmall")
                              .parentElement.parentElement.parentElement.parentElement
                              .querySelector("div:nth-child(2)>:nth-child(4)>div>span>span")
                              ?.innerText?.trim();
                        }
                        
    
                        businessDetail.location = businessSection
                          .querySelector(
                            "div>div:nth-child(4)>div>div>div>div:nth-child(2)>:nth-child(4)>div>:nth-child(2)>:nth-child(2)"
                          )
                          ?.innerText?.trim();
                          if (!businessDetail.location) {
                            businessDetail.location = businessSection
                              .querySelector(".fontHeadlineSmall")
                              .parentElement.parentElement.parentElement.parentElement
                              .querySelector("div:nth-child(2)>:nth-child(4)>div>:nth-child(2)>:nth-child(2)")
                              ?.innerText?.trim();
                        }
    
    
                        businessDetail.rating = parseFloat(
                          businessSection.querySelector(
                            "div>div:nth-child(4)>div>div>div>div:nth-child(2)>:nth-child(3)>div>:nth-child(2)>span>span"
                          )?.innerText
                        );
                        if (!businessDetail.rating) {
                          businessDetail.rating = parseFloat( businessSection
                            .querySelector(".fontHeadlineSmall")
                            .parentElement.parentElement.parentElement
                            .querySelector("div:nth-child(2)>:nth-child(3)>div>:nth-child(2)>span>span")
                            ?.innerText
                          );
                        }
    
                        let rawPhoneText = businessSection.querySelector(
                          "div>div:nth-child(4)>div>div>div>div:nth-child(2)>:nth-child(4)>:nth-child(2):nth-child(2)>:nth-child(2)>span:nth-child(2)"
                        )?.innerText;
                        if (!rawPhoneText) {
                          rawPhoneText = businessSection.querySelector(
                            ".fontHeadlineSmall"
                          )
                            .parentElement.parentElement.parentElement
                            .querySelector(
                              "div:nth-child(2)>:nth-child(4)>:nth-child(2):nth-child(2)>:nth-child(2)>span:nth-child(2)"
                            )?.innerText;
                        }
                        if (rawPhoneText) {
                          businessDetail.formattedPhoneNumber = isValidPhoneNumber(
                            rawPhoneText
                          )
                            ? formatPhoneNumber(rawPhoneText)
                            : "";
                        }
    
                        let rawPartialAddress = businessSection
                        .querySelector("div>div:nth-child(4)>div>div>div>div:nth-child(2)>:nth-child(4)")
                        ?.querySelector(":scope div>span:last-of-type > span:nth-child(2)")?.innerText;
                        
                        if (!rawPartialAddress) { 
                          rawPartialAddress = businessSection.querySelector(
                            ".fontHeadlineSmall"
                          )
                            .parentElement.parentElement.parentElement
                            .querySelector(
                              "div:nth-child(2)>:nth-child(4)>div>span:nth-child(2)>span:nth-child(2)"
                            )?.innerText;
                        }
                        if (rawPartialAddress) {
                          if (!rawPartialAddress.match(/^\+\d/)) {
                            businessDetail.formattedPartialAddress =
                              formatAddress(rawPartialAddress);
                            businessDetail.rawPartialAddress = rawPartialAddress;
                          } else {
                            businessDetail.rawPartialAddress = "";
                            businessDetail.formattedPartialAddress = "";
                          }
                        }
    
                        const placeIdHref =
                          businessSection.querySelector("div>div>a")?.href;
                        if (placeIdHref) {
                          const placeId = extractPlaceIdFromHref(placeIdHref);
                          businessDetail.placeId = placeId;
                        }

                        if (!businessDetail.placeId) {
                          const anchorTags = businessSection.querySelectorAll("div a[href]");

                          if(anchorTags?.length > 0) { 
                            const hrefs = Array.from(anchorTags).map((a) => a.href);
                            const placeIdHref = hrefs?.filter((href) =>
                              extractPlaceIdFromHref(href)
                            )?.[0];

                            if (placeIdHref) {
                              const placeId = extractPlaceIdFromHref(placeIdHref);
                              businessDetail.placeId = placeId;
                            }
                          }
                        }
    
                        businessesDetails.push(businessDetail);
                      }
                    } catch (error) {
                    }

                  });

                  let bussinessRankList = businessesDetails.map(
                    (businessDetail) => {
                      let business = {
                        name: businessDetail.title || "",
                        address: businessDetail.rawPartialAddress || "",
                        shortAddr: businessDetail.formattedPartialAddress || "",
                        shortPhone: businessDetail.formattedPhoneNumber || "",
                        rat: businessDetail.rating || "",
                        pid: businessDetail.placeId || "",
                      };

                      if(businessDetail.ads) {
                        business.ads = 1;
                      }

                      return business;
                    }
                  );

                  const businessList = trimBusinessListIfExceedsTheUrlLengthLimit(
                    teleportDashboardUrl,
                    urlParams,
                    teleportBusinessDetail,
                    bussinessRankList
                  );

                  urlParams.append("businesses", JSON.stringify(businessList));
                  urlParams.append("searchedOnce", true);
                  urlParams.append(
                    "teleport-business",
                    JSON.stringify(teleportBusinessDetail)
                  );
                  window.location.replace(
                    `${teleportDashboardUrl}?${urlParams.toString()}`
                  );
                };

                let businessElementCount = getBusinesses(
                  document.querySelector(businessElementSelector).children
                ).length;

                let container = document.querySelector(businessElementSelector);
                container.scrollTo(0, container.scrollHeight);
                scrollWithTimeLagTeleport(
                  businessElementCount,
                  maxBusinessCount,
                  businessElementSelector,
                  onScrolledToBottomTeleport
                );
              } else if (
                document.querySelector(
                  "#pane>div>div>div>div>div:nth-child(2)>div>div>div>h1"
                )?.innerText === urlParams.get("businessName")
              ) {
                // What scenario does this code run?
                urlParams.append("result", businessRank);
                urlParams.append("searchedOnce", true);
                window.location.replace(
                  `${teleportDashboardUrl}?${urlParams.toString()}`
                );
              }
            } catch(e) {
              forwardToErrorPage({
                auditType: "teleport",
                error: e,
                newTab: false,
              })
              return;
            }
          });
        }, 1000);
      }
    };
  }
};

const isValidPhoneNumber = (value) => {
  const regexp = new RegExp(
    `^[\s()+-]*([0-9][\s()+-]*){${slicePhoneNumberLimit},20}$`
  );
  return regexp.test(value?.replace(/\s/g, ""));
};

const trimBusinessListIfExceedsTheUrlLengthLimit = (
  teleportDashboardUrl,
  existingUrlParams,
  teleportBusinessDetail,
  bussinessRankList
) => {
  try {
    const cloudfrontUrlLimit = 8100;

    let encodedUrl = buildAndEncodeUrl(
      teleportDashboardUrl,
      existingUrlParams,
      teleportBusinessDetail,
      bussinessRankList
    );

    if (encodedUrl.length < cloudfrontUrlLimit) {
      return bussinessRankList;
    }

    encodedUrl = buildAndEncodeUrl(
      teleportDashboardUrl,
      existingUrlParams,
      teleportBusinessDetail,
      bussinessRankList.slice(0, 12)
    );

    if (encodedUrl.length < cloudfrontUrlLimit) {
      return bussinessRankList.slice(0, 12);
    }

    return bussinessRankList.slice(0, 6);
  } catch {
    return bussinessRankList;
  }
};

const buildAndEncodeUrl = (
  teleportDashboardUrl,
  existingUrlParams,
  teleportBusinessDetail,
  bussinessRankList
) => {
  const urlSearchParams = new URLSearchParams(existingUrlParams.toString());

  urlSearchParams.append("businesses", JSON.stringify(bussinessRankList));
  urlSearchParams.append("searchedOnce", true);
  urlSearchParams.append(
    "teleport-business",
    JSON.stringify(teleportBusinessDetail)
  );

  return `${teleportDashboardUrl}?${urlSearchParams.toString()}`;
};

export { findBusinessRankForTeleport };
