import { codeConstants, geMode } from "../feature-flag-and-constants";
import * as Utility from "../utility";

function GetValuesFromPageScript(windowArray, isCodeInDirectPage) {
  let categories = [];
  let businessName = "";
  let phoneNumber = "";
  let placeId = "";
  let kgId = "";
  let address = "";
  let website = "";
  let latlong;
  let averageReviewRating, numberOfReviews;
  let isBusinessVerified = false;
  let cidValue = "";
  let businessProfileId = "";
  let subsetOfOriginalArray;

  if (isCodeInDirectPage === true) {
    subsetOfOriginalArray = windowArray[6];
  } else if (typeof (windowArray[0][1][0][14]) !== "undefined") {
    subsetOfOriginalArray = windowArray[0][1][0][14];
  } else if (typeof (windowArray[0][1][1][14]) !== "undefined") {
    subsetOfOriginalArray = windowArray[0][1][1][14];
  }
  try {
    categories = subsetOfOriginalArray[13];
    businessName = subsetOfOriginalArray[11];


    if (
      subsetOfOriginalArray[178] && subsetOfOriginalArray[178][0] && subsetOfOriginalArray[178][0][3]) {
      phoneNumber = subsetOfOriginalArray[178][0][3];
    } else {
      phoneNumber = "";
    }
    if (subsetOfOriginalArray[4] && subsetOfOriginalArray[4][7] && subsetOfOriginalArray[4][8]) {
      averageReviewRating = subsetOfOriginalArray[4][7];
      numberOfReviews = subsetOfOriginalArray[4][8];
    } else {
      averageReviewRating = 0;
      numberOfReviews = 0;
    }
    if (subsetOfOriginalArray && subsetOfOriginalArray[78]) {
      placeId = subsetOfOriginalArray[78];
    } else {
      placeId = "";
    }

    if (subsetOfOriginalArray && subsetOfOriginalArray[89]) {
      kgId = subsetOfOriginalArray[89];
    } else {
      kgId = "";
    }

    if (subsetOfOriginalArray && subsetOfOriginalArray[2]) {
      address = subsetOfOriginalArray[2];
    } else {
      address = "";
    }

    if (subsetOfOriginalArray && subsetOfOriginalArray[7] && subsetOfOriginalArray[7][0]) {
      website = subsetOfOriginalArray[7][0];
    } else {
      website = "";
    }

    if (subsetOfOriginalArray && subsetOfOriginalArray[9] && subsetOfOriginalArray[9][2] && subsetOfOriginalArray[9][3]) {
      latlong = {
        "lat": subsetOfOriginalArray[9][2],
        "long": subsetOfOriginalArray[9][3]

      };
    } else {
      latlong = {
        "lat": "",
        "long": ""

      };
    }
    if (subsetOfOriginalArray && subsetOfOriginalArray[49]) {
      isBusinessVerified = false;
    } else {
      isBusinessVerified = true;
    }

    try {
      if (subsetOfOriginalArray && subsetOfOriginalArray[181] && subsetOfOriginalArray[181][5]) {
        businessProfileId = subsetOfOriginalArray[181][5];
      } else if (subsetOfOriginalArray && subsetOfOriginalArray[227] && subsetOfOriginalArray[227][0] && subsetOfOriginalArray[227][0][5]) {
        businessProfileId = subsetOfOriginalArray[227][0][5];
      } else {
        businessProfileId = "";
      }
      
    } catch (error) {
      businessProfileId = "";
    }
   
    try {
      if ( subsetOfOriginalArray && subsetOfOriginalArray[126] && subsetOfOriginalArray[126][4] ) {

          let url = new URL(subsetOfOriginalArray[126][4]);
          cidValue = url.searchParams.get('ludocid');
    
      } else if ( subsetOfOriginalArray && subsetOfOriginalArray[126] && subsetOfOriginalArray[126][5] ) {

        let url = new URL(subsetOfOriginalArray[126][5]);
        cidValue = url.searchParams.get('ludocid');
  
    } else {
        cidValue = "";
      }
      
    } catch (error) {
      cidValue = "";
    }



  } catch{}


  return { categories, phoneNumber, businessName, numberOfReviews, averageReviewRating, placeId, kgId, address, website, latlong, isBusinessVerified, cidValue, businessProfileId };
}


let createGUID = () => {

  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
    return v.toString(16);
  });
}

let writeBusinessValueToStorageAndOpenExternalWindow = (existingValue, uniqueBusinessDetails, uniqueGUIDKey, auditType, dataUniqueCode) => {
  if (existingValue === undefined) {
    existingValue = [uniqueBusinessDetails];
  }
  else {
    existingValue.push(uniqueBusinessDetails);
  }

  let maximumStorageSizeAudit = codeConstants["audit-dashboard"]["maximum-array-size-for-audit-details"];
  if (existingValue.length > maximumStorageSizeAudit) {
    existingValue = existingValue.slice(-maximumStorageSizeAudit);
  }

  chrome.storage.local.set({ 'ge-business-details-key': existingValue }, function () {


    let teleportDashboardUrl = codeConstants["teleport-dashboard"]["dashboard-url"],
        aiAuditPageUrl = codeConstants["ai-audit"]["dashboard-url"],
        basicAuditUrl = codeConstants["audit-dashboard"]["basic-audit-url"],
        reviewAuditUrl = codeConstants["audit-dashboard"]["review-audit-url"];      
    if (auditType === "basic-audit") {
      Utility.getUrlAppendUserInformationAndForward(basicAuditUrl + "?ge-mode=business-details&audit-type=basic&unique-code=" + uniqueGUIDKey + "&data-unique-code=" + dataUniqueCode + "&dashboard-data=" + encodeURIComponent(JSON.stringify(uniqueBusinessDetails["value"])));
    } else if( auditType === "review-audit") {
      if (uniqueBusinessDetails["value"]["number-of-review"] > 0) {
        // Add n to the end of the URL to prevent the URL from being truncated because of added fragment (#) by google map
        window.open("https://search.google.com/local/reviews?hl=en-us&placeid=" + uniqueBusinessDetails["value"]["place-id"] + "&ge-mode="+  geMode["scrapping-review-page"]["ge-mode"] +  "&unique-code=" + uniqueGUIDKey + "&number-of-reviews=" + uniqueBusinessDetails["value"]["number-of-review"] + "n");
      } else {
        Utility.getUrlAppendUserInformationAndForward(reviewAuditUrl + "?ge-mode=business-details&audit-type=review&unique-code=" + uniqueGUIDKey + "&dashboard-data=" + encodeURIComponent(JSON.stringify(uniqueBusinessDetails["value"])));
      }
    } else if (auditType === "post-audit") {
      window.open("https://www.google.com/search?kgmid=" + uniqueBusinessDetails["value"]["kg-id"] + "&hl=en-us&ge-mode=" + geMode["scrapping-post-page"]["ge-mode"] + "&unique-code=" + uniqueGUIDKey +"#lpstate=pid:-1");
    } else if (auditType === "teleport") {
      Utility.getUrlAppendUserInformationAndForward(teleportDashboardUrl + "?ge-mode=business-details&audit-type=basic&unique-code=" + uniqueGUIDKey + "&dashboard-data=" + encodeURIComponent(JSON.stringify(uniqueBusinessDetails["value"])));
    } else if (auditType === "ai-audit") {
      Utility.getUrlAppendUserInformationAndForward(aiAuditPageUrl + "?ge-mode=business-details&audit-type=ai&unique-code=" + uniqueGUIDKey + "&dashboard-data=" + encodeURIComponent(JSON.stringify(uniqueBusinessDetails["value"])));
    }
  });
}



let AuditInformationLogic = (urlLocation, propertyHowAccessedCategories, HowCorrectCategoryFoundEnum, auditType) => {
  if (propertyHowAccessedCategories === HowCorrectCategoryFoundEnum.GMBScriptOnPage) {
    try {
      let processedTxt = Utility.pickupScriptStringFromPage();

      if (processedTxt && processedTxt.length > 5) {
        const windowArray = JSON.parse(processedTxt.substring(5));

        let { categories, businessName, numberOfReviews, averageReviewRating, placeId, kgId, address, website, latlong, phoneNumber, isBusinessVerified, cidValue, businessProfileId } = GetValuesFromPageScript(windowArray, true);

        let { uniqueGUIDKey, uniqueBusinessDetails } = createJSONForStorage(uniqueBusinessDetails, businessName, categories, placeId, kgId, averageReviewRating, numberOfReviews, address, website, latlong, phoneNumber, isBusinessVerified, cidValue, businessProfileId);
        writeToStorageAndForwardHandler(uniqueBusinessDetails, uniqueGUIDKey, auditType, windowArray);

      }
    } catch(e) {
      Utility.forwardToErrorPage({
        auditType: auditType,
        error: e
      });
      return;
    }
  } else if (propertyHowAccessedCategories === HowCorrectCategoryFoundEnum.GMBScriptWithDirectLocation || propertyHowAccessedCategories === HowCorrectCategoryFoundEnum.GMBScriptWithNearLocation) {
    callGoogleMap(urlLocation, (data) => {
      try {
        const processedTxt = data && data[3] ? data[3][2] : "";

        if (processedTxt && processedTxt.length > 5) {
          const windowArray = JSON.parse(processedTxt.substring(5));
          try {

            let { categories, businessName, numberOfReviews, averageReviewRating, placeId, kgId, address, website, latlong, phoneNumber, isBusinessVerified, cidValue, businessProfileId } = GetValuesFromPageScript(windowArray, false);

            let { uniqueGUIDKey, uniqueBusinessDetails } = createJSONForStorage(uniqueBusinessDetails, businessName, categories, placeId, kgId, averageReviewRating, numberOfReviews, address, website, latlong, phoneNumber, isBusinessVerified, cidValue, businessProfileId);

            writeToStorageAndForwardHandler(uniqueBusinessDetails, uniqueGUIDKey, auditType, windowArray);

          } catch(e) {
            throw new Error(e);
           }

        } else {
        }
      } catch(e) {
        Utility.forwardToErrorPage({
          auditType: auditType,
          error: e
        });
        return;
      }
    })
  } else if (propertyHowAccessedCategories === HowCorrectCategoryFoundEnum.ExactLink) {
    callGoogleMap(urlLocation, (data) => {
      try {
        const processedTxt = data && data[3] ? data[3][6] : "";
        if (processedTxt && processedTxt.length > 5) {        
          const windowArray = JSON.parse(processedTxt.substring(5));
          const categories = windowArray[6][13];

          if (categories && categories.length) {
            let {
              categories,
              businessName,
              numberOfReviews,
              averageReviewRating,
              placeId,
              kgId,
              address,
              website,
              latlong,
              phoneNumber,
              isBusinessVerified,
              cidValue, 
              businessProfileId
            } = GetValuesFromPageScript(windowArray, true);
            let { uniqueGUIDKey, uniqueBusinessDetails } = createJSONForStorage(
              uniqueBusinessDetails,
              businessName,
              categories,
              placeId,
              kgId,
              averageReviewRating,
              numberOfReviews,
              address,
              website,
              latlong,
              phoneNumber,
              isBusinessVerified,
              cidValue,
              businessProfileId
            );
            writeToStorageAndForwardHandler(uniqueBusinessDetails, uniqueGUIDKey, auditType, windowArray);
          }
        }
      } catch (e) {
        Utility.forwardToErrorPage({
          auditType: auditType,
          error: e
        });
        return;
      }
    });
  }
}

let scrollWithTimeLag = (totalNumberOfReview, maximumReviewAllowed) => {
  setTimeout(function () {
    let presentNumberOfReviewsOnPage = document.getElementsByClassName("mapsConsumerUiSubviewSectionReview__section-review-titles mapsConsumerUiSubviewSectionReview__section-review-titles-with-menu").length;
    let element = document.getElementsByClassName("section-layout section-scrollbox mapsConsumerUiCommonScrollable__scrollable-y mapsConsumerUiCommonScrollable__scrollable-show")[0];

    element.scrollTo(0, element.scrollHeight);
    if (!(presentNumberOfReviewsOnPage === totalNumberOfReview || presentNumberOfReviewsOnPage >= maximumReviewAllowed)) {
      scrollWithTimeLag(totalNumberOfReview, maximumReviewAllowed);
    } else {
      displayFinalReviewarray();
    }
  }, 2000);
}


const callGoogleMap = (url, callback) => {
  const xhr = new XMLHttpRequest();
  xhr.onreadystatechange = () => {
    if (xhr.readyState === XMLHttpRequest.DONE) {
      const status = xhr.status;
      if (status === 0 || (status >= 200 && status < 400)) {
        try {
          const searchString = "window.APP_INITIALIZATION_STATE=";
          const codeStart = xhr.response.indexOf(searchString);
          const codeEnd = xhr.response.indexOf(
            "window.APP_FLAGS",
            codeStart
          );
          const code = xhr.response.substring(
            codeStart + searchString.length,
            codeEnd
          );

          //slice the string to remove simicolon before parsing
          const WindowInitializationState = JSON.parse(code.slice(0, -1));
          callback(WindowInitializationState);
        } catch {
          callback("");
        }
      } else {
        return "";
      }
    }
  };

  //Request data from server(google map) to parse APP_INITIALIZATION_STATE value
  xhr.open("GET", url);
  xhr.send();
};

const writeToStorageAndForwardHandler = (uniqueBusinessDetails, uniqueGUIDKey, auditType, windowArray) => {
  if (auditType === 'basic-audit') {
    const dataUniqueCode = Utility.createGuid();
    sendBasicAuditDataToServer({
      "url": window.location.href,
      "windowArray": getOriginalDataArrayFromWindowArray(windowArray),
      "dataUniqueCode": dataUniqueCode
    }).finally(() => {
      // for backward compatibility
      writeToStorageAndForward(uniqueBusinessDetails, uniqueGUIDKey, auditType, dataUniqueCode);
    });

    return;
  }
  uniqueBusinessDetails["window_array"] = getOriginalDataArrayFromWindowArray(windowArray);
  writeToStorageAndForward(uniqueBusinessDetails, uniqueGUIDKey, auditType, "");
}

function writeToStorageAndForward(uniqueBusinessDetails, uniqueGUIDKey, auditType, dataUniqueCode) {
  chrome.storage.local.get(['ge-business-details-key'], function (result) {
    writeBusinessValueToStorageAndOpenExternalWindow(result['ge-business-details-key'], uniqueBusinessDetails, uniqueGUIDKey, auditType, dataUniqueCode);
  });
}

function createJSONForStorage(uniqueBusinessDetails, businessName, categories, placeId, kgId, averageReviewRating, numberOfReviews, address, website, latlong, phoneNumber, isBusinessVerified, cidValue, businessProfileId) {
  let uniqueGUIDKey = createGUID();
  uniqueBusinessDetails = (
    {
      "key": uniqueGUIDKey,
      "value": {
        "business-name": businessName,
        "categories": categories,
        "place-id": placeId,
        "kg-id": kgId,
        "average-review-rating": averageReviewRating,
        "number-of-review": numberOfReviews,
        "address": address,
        "website": website,
        "latlong": latlong,
        "phone-number": phoneNumber,
        "is-business-verified": isBusinessVerified,
        "cid-value": cidValue, 
        "business-profile-id": businessProfileId
      }
    });
  return { uniqueGUIDKey, uniqueBusinessDetails };
}

const sendBasicAuditDataToServer = (data) => {
  return new Promise((resolve, reject) => {
    const sendDataApiUrl = codeConstants["audit-dashboard"]["send-data-api-url"];

    const dataObject = {
      "url": data.url,
      "rawData": data.windowArray,
    }

    Utility.apiPostRequest(sendDataApiUrl, {
      "unique_code": data.dataUniqueCode,
      "data": JSON.stringify(dataObject),
      "feature_type": "basic-audit"
    }).then((response) => {
      resolve(response);
    }).catch((error) => {
      reject(error);
    })
  })
}

function getOriginalDataArrayFromWindowArray(windowArray) {
  if (windowArray[6] !== null && windowArray[6] !== undefined) {
      return windowArray[6];
  } else if (typeof (windowArray[0][1][0][14]) !== undefined && windowArray[0][1][0][14] !== null) {
      return windowArray[0][1][0][14];
  } else if (typeof (windowArray[0][1][1][14]) !== undefined && windowArray[0][1][1][14] !== null) {
      return windowArray[0][1][1][14];
  }

  return windowArray;
}


export { AuditInformationLogic };

