      finalReviewArrayList: finalReviewArrayList, 
      currentRating: currentRating,
      reviewWords: reviewWords,
      totalReviewCount: totalReviewCount,
      uniqueCodeOnPage: uniqueGUIDKey,
      dataUniqueCode: dataUniqueCode,
      totalReviewsAnalyzed: finalReviewArrayList?.length,
      totalReviewsAnalyzedWithResponse: totalReviewsAnalyzedWithResponse,
