let getWordFrequencies = (words, stopWordList) => {
  let oneWordFrequencies = {},
      twoWordFrequencies = {};

  try {
    for (let index = 0; index < words.length; index++) {
      let firstWord = words[index].toLowerCase(),
          secondWord = words[index + 1]?.toLowerCase();
  
  
      if (!firstWord || stopWordList.indexOf(firstWord) > 0 || firstWord.length === 1) 
        continue;
  
      if (oneWordFrequencies[firstWord]) {
        oneWordFrequencies[firstWord] += 1;
      }
      else {
        oneWordFrequencies[firstWord] = 1;
      }
  
  
      if (!secondWord || stopWordList.indexOf(secondWord) > 0 || secondWord.length === 1)
        continue;
  
      if (twoWordFrequencies[`${firstWord} ${secondWord}`]) {
        twoWordFrequencies[`${firstWord} ${secondWord}`] += 1;
      }
      else {
        twoWordFrequencies[`${firstWord} ${secondWord}`] = 1;
      }
  
        
    }
  
    oneWordFrequencies = Object.keys(oneWordFrequencies).map(k => {
      let obj = {};
      obj[String(k).slice(0, maximumOneWordCharLength)] = oneWordFrequencies[k];
      return obj;
    });
  
    twoWordFrequencies = Object.keys(twoWordFrequencies).map(k => {
      let obj = {};
      obj[String(k).slice(0, maximumTwoWordCharLength)] = twoWordFrequencies[k];
      return obj;
    });
      
  }
  catch (err) {
  }

  return { oneWordFrequencies, twoWordFrequencies };
}



let cleanUpArrayAndGetYearCounterAsJSON = (yearPastCounter) => {

  let uniqueYearPastValues = returnUniqueValuesInArray(yearPastCounter);
  let yearPastDateFormatCounter = [];

  for (let i = 0; i < uniqueYearPastValues.length; i++) {

    yearPastDateFormatCounter.push(
      {
        "year-past-in-review": uniqueYearPastValues[i],
        "number-of-review": countNumberOfTimeValueExistInArray(uniqueYearPastValues[i], yearPastCounter)

      }
    );
  }

  return yearPastDateFormatCounter;


}

let getYearPastAndReturnApproximateMonth = (yearMonthJSONFormatted, fromOneOrMoreYearAgoRatings) => {


  let listOfApproximatedTimePeriod = [],
      ratingData = [];

  for (let i = 0; i < yearMonthJSONFormatted.length; i++) {
    let thisYearRatings = fromOneOrMoreYearAgoRatings.filter(f => f.year === yearMonthJSONFormatted[i]["year-past-in-review"]);
    let averageRatingForThisYear = thisYearRatings.reduce((totalRating, currentRating) => currentRating.rating + totalRating, 0) / thisYearRatings.length;

    let totalReview = yearMonthJSONFormatted[i]["number-of-review"];

    for (let j = 0; j < totalReview; j++) {
      let date = addMonths((addYears(new Date(), -yearMonthJSONFormatted[i]["year-past-in-review"])), -j % 12);
      listOfApproximatedTimePeriod.push(date);
      ratingData.push({
        date: date,
        year: parseInt(yearMonthJSONFormatted[i]["year-past-in-review"]),
        rating: averageRatingForThisYear
      });

    }
  }
  return [listOfApproximatedTimePeriod, ratingData];
}

let getMonthlyAverageRating = (ratings) => {

  //format date group element by month
  let ratingsGroupByMonth = ratings.map(rating => {

      try {
        rating.date = format(rating.date, "MM/15/yyyy");

        //This below logic is to move middle of the month for present month to today
        if (format(new Date, "MM/yyyy") === format(parse(rating.date, "MM/dd/yyyy", new Date()), "MM/yyyy")) {
          rating.date = format(new Date, "MM/dd/yyyy");
        }
      } catch (e) {
        forwardToErrorPage({
          auditType: "review",
          error: e,
          newTab: false,
        });
      }

      return rating;
    })
    .reduce((acc, cur) => {
      acc[cur.date] = [...acc[cur.date] || [], cur];
      return acc;
    }, {});


  let processedRatings = Object.keys(ratingsGroupByMonth)
    .map(date => {
      let averageRating = ratingsGroupByMonth[date].reduce((totalRating, currentRating) => currentRating.rating + totalRating, 0) / ratingsGroupByMonth[date].length;

      return {
        date: date,
        rating: averageRating <= 5 ? parseFloat(averageRating.toFixed(2)) : 5 
      }
    });

  return processedRatings;
}

let getReviewCountAndRatingPerDateFromReviewList = (reviewString, currentRating, totalReviewCount) => {

  let orderedReviewContainingReviewDate = [];
  let yearPastAsInt = [];
  let ratings = [];

  for (let i = 0; i < reviewString.length; i++) {

    let date = null,
        year = 0;

    if (reviewString[i]["review-time-organized"].hours != 0) {
      date = addHours(new Date(), -reviewString[i]["review-time-organized"].hours)
      orderedReviewContainingReviewDate.push(date);
    } else if (reviewString[i]["review-time-organized"].days !== 0) {
      date = addDays(new Date(), -reviewString[i]["review-time-organized"].days)
      orderedReviewContainingReviewDate.push(date);
    } else if (reviewString[i]["review-time-organized"].weeks !== 0) {
      date = addDays(new Date(), -(reviewString[i]["review-time-organized"].weeks) * 7);
      orderedReviewContainingReviewDate.push(date);
    } else if (reviewString[i]["review-time-organized"].months !== 0) {
      date = addMonths(new Date(), -reviewString[i]["review-time-organized"].months);
      orderedReviewContainingReviewDate.push(date);
    } else if (reviewString[i]["review-time-organized"].years !== 0) {
      date = reviewString[i]["review-time-organized"].years
      year = reviewString[i]["review-time-organized"].years;
      yearPastAsInt.push(
        reviewString[i]["review-time-organized"].years
      );
    }

    ratings.push({
      date: date,
      year: year,
      rating: parseFloat(reviewString[i]['review-star-rating'])
    });

  };

  let cleanedupMonthYearCounterAsJSON = cleanUpArrayAndGetYearCounterAsJSON(yearPastAsInt);

  let [approximateMonthYearValue, approximatedRatings] = getYearPastAndReturnApproximateMonth(cleanedupMonthYearCounterAsJSON, ratings.filter(r => r.year > 0));

  ratings = [...ratings.filter(r => r.year === 0), ...approximatedRatings];

  let processedRatings = getMonthlyAverageRating(ratings, currentRating, totalReviewCount);


  let finalDateList = orderedReviewContainingReviewDate.concat(approximateMonthYearValue);

  // Pushing every review of the month to the middle of the month
  let finalDateListOrganized = (finalDateList.map(value => (format(value, "MM/15/yyyy"))));

  let tempCountNumberOfTimeExist;
  let orderedReviewTimeAndCounter = [];

  returnUniqueValuesInArray(finalDateListOrganized).forEach(value => {

    tempCountNumberOfTimeExist = countNumberOfTimeValueExistInArray(value, finalDateListOrganized);

    //This below logic is to move middle of the month for present month to today
    if (format(new Date, "MM/yyyy") === format(parse(value, "MM/dd/yyyy", new Date()), "MM/yyyy")) {
      value = format(new Date, "MM/dd/yyyy");
    }

    orderedReviewTimeAndCounter.push({
      "month-year": value,
      "number-of-time-exist": tempCountNumberOfTimeExist
    });
  });



  return [ orderedReviewTimeAndCounter, processedRatings ];
}

let getReviewValues = (reviewList) => {

  let numberOfReviewWithPhoto = 0;
  let numberOfReviewFromLocalGuides = 0;
  let sumOfNumberOfReviewByReviewer = 0;
  let sumOfReviews = 0;
  let sumOfAnalyzedReviews = 0;
  let sumOfAnalyzedReviewsWithResponses = 0;

  reviewList.forEach(value => {
    if (value["number-of-photos-in-review"] !== 0) {
      numberOfReviewWithPhoto++;
    }
    if (value["is-a-local-guide"] == true) {
      numberOfReviewFromLocalGuides++;
    }
    sumOfNumberOfReviewByReviewer += parseInt(value["number-of-review-provided-by-reviewer"]);
    sumOfReviews += parseInt(value["review-star-rating"]);

  });
  return {
    "number-of-reviews-with-photos": numberOfReviewWithPhoto,
    "number-of-reviews-by-local-guides": numberOfReviewFromLocalGuides,
    "average-number-of-review-by-reviewer": (sumOfNumberOfReviewByReviewer / reviewList.length).toFixed(2),
    "average-of-reviews-calculated": (sumOfReviews / reviewList.length).toFixed(2)
  };
}

let cleanUpXYGraphData = ( orderedREviewTimeAndCounter, ratings,  specificValueFromStorage, finalReviewArrayList) => {
  let graphData = [];
  orderedREviewTimeAndCounter
  .sort((a, b) => parse(a['month-year'], 'MM/dd/yyyy', new Date()) - parse(b['month-year'], 'MM/dd/yyyy', new Date()))
  .forEach((value, index) => {
    graphData.push([value["month-year"], value["number-of-time-exist"], ratings.filter(r => r.date === value["month-year"])[0].rating]);

  });

  let yAxisValues = graphData.map(g => g[1]);

  graphData = graphData.map((element, index) => {
    element[1] = yAxisValues.slice(0, index + 1).reduce((a, b) => a + b);
    return element;
  });

  graphData = graphData.map(element => {
    element[1] = element[1] + specificValueFromStorage['value']['number-of-review'] - finalReviewArrayList.length;
    return element;
  });

  return graphData;
}

let rankWordFrequencies = (wordFrequencies) => {
  return wordFrequencies.sort((a, b) => {
    let valueA = a[Object.keys(a)[0]],
        valueB = b[Object.keys(b)[0]];

    if (valueA > valueB) 
      return -1;
    
    if (valueA < valueB)
      return 1;

    return 0;
  });

}


let writeOrUpdateReviewsToValue = (params) => {

  for (let i = params.resultOfStorage.length - 1; i >= 0; i--) {
    if (params.uniqueCodeOnPage && params.resultOfStorage[i]["key"] === params.uniqueCodeOnPage) {

      let [orderedREviewTimeAndCounter, ratings] = getReviewCountAndRatingPerDateFromReviewList(params.finalReviewArrayList, params.currentRating, params.totalReviewCount);

      let graphData = cleanUpXYGraphData(orderedREviewTimeAndCounter, ratings, params.resultOfStorage[i], params.finalReviewArrayList);
      params.resultOfStorage[i]["value"]["xy-axis-Values"] = graphData;
      let calculatedReviewValues = getReviewValues(params.finalReviewArrayList);
      params.resultOfStorage[i]["value"]["review-calculated-values"] = calculatedReviewValues;


      let reviewAuditUrl = codeConstants['audit-dashboard']['review-audit-url'];

      let { oneWordFrequencies, twoWordFrequencies } = getWordFrequencies(params.reviewWords, stopWordList);

      oneWordFrequencies = JSON.stringify(enforceMaximumCharacters(rankWordFrequencies(oneWordFrequencies)));

      twoWordFrequencies = JSON.stringify(enforceMaximumCharacters(rankWordFrequencies(twoWordFrequencies)));
      const gmb_metadata = params.resultOfStorage[i]["window_array"];

      const dataObject = {
        "ge-mode": "business-details",
        "audit-type": "review",
        "unique-code": params.uniqueCodeOnPage,
        "dashboard-data": JSON.stringify(params.resultOfStorage[i]["value"]),
        "one-word-frequencies": oneWordFrequencies,
        "two-word-frequencies": twoWordFrequencies,
        "total-reviews-analyzed": params.totalReviewsAnalyzed ?? 0,
        "total-reviews-analyzed-with-response": params.totalReviewsAnalyzedWithResponse ?? 0,
      };


      const reviewModalHtmlFragment = document.querySelector(".review-dialog-body")?.outerHTML;
      if (!reviewModalHtmlFragment) {
        reject(new Error("Review modal html fragment not found"));
        return;
      }

      const chunks = chunkByBytes(reviewModalHtmlFragment, 3);
      const dataUniqueCodes = [];
      const chunkedPayloads = chunks.map((chunk, index) => {
        const dataUniqueCode = `${params.dataUniqueCode}_v${index + 1}`;
        dataUniqueCodes.push(dataUniqueCode);
        return {
          "unique_code": dataUniqueCode,
          "data": JSON.stringify({
            "review_modal_html_fragment": chunk.chunkData,
            "url": params.url,
            "gmb_metadata": gmb_metadata
          }),
          "feature_type": "review-audit"
        }
      });
      dataObject['data-unique-code'] = dataUniqueCodes.join(',');
      const queries = trimReviewParamsIfExceedsTheUrlLengthLimit(reviewAuditUrl, dataObject);
      sendReviewDataToServer(chunkedPayloads).finally(() => {
        getUrlAppendUserInformationAndForward(`${reviewAuditUrl}?${queries.toString()}`, true);
      });

    }

  }
}

const trimReviewParamsIfExceedsTheUrlLengthLimit = (dashboardUrl, queries) => {
  // limit is 8100, but we are keeping it 7850 to allocate 250 for the user-information param
  const cloudfrontUrlLimit = 7850;

  let encodedUrl = buildAndEncodeUrl(dashboardUrl, queries);
  if (encodedUrl.length < cloudfrontUrlLimit) {
    return new URLSearchParams(queries);
  }

  const { "two-word-frequencies": twoWordFrequencies, ...reducedQueries } =
    queries;
  encodedUrl = buildAndEncodeUrl(dashboardUrl, reducedQueries);
  if (encodedUrl.length < cloudfrontUrlLimit) {
    return new URLSearchParams(reducedQueries);
  }

  const { "one-word-frequencies": oneWordFrequencies, ...reducedQueries2 } =
    reducedQueries;
  encodedUrl = buildAndEncodeUrl(dashboardUrl, reducedQueries2);
  return new URLSearchParams(reducedQueries2);
};

const buildAndEncodeUrl = (dashboardUrl, queries) => {
  const urlSearchParams = new URLSearchParams(queries);
  return `${dashboardUrl}?${urlSearchParams.toString()}`;
};

const displayFinalReviewarray = () => {
  reviewAuditModalVersion = getReviewAuditModalVersion();

  if(reviewAuditModalVersion == REVIEW_AUDIT_MODAL_VERSION.v1) {
    displayFinalReviewArrayV1();
  } else {

    // click 'More' buttons for all reviews
    document.querySelectorAll('div[jsdata] > div[data-ved] div[data-id][data-hveid] > div:nth-child(3) > div > a[role="button"][data-ved]')
      .forEach(element => { element.click() });

    // Add a delay before scraping the reviews to ensure all 'More' buttons are clicked.
    setTimeout(displayFinalReviewArrayV2, 1000);
  }
}

export { displayFinalReviewarray };

