const isLocalFinder = () => {
  const isInSearchPage =
    `${getCurrentDomainHostName()}${window.location.pathname}` ===
    `${getCurrentDomainHostName()}/search`;
  const searchForm = document.querySelector("#searchform");
  const appBar = document.querySelector("#appbar");
  const search = document.querySelector("#search");
  const appBarRatingItems = document.querySelector("#appbar [role='menu']");
  const appBarHoursItems = document.querySelector("#appbar [role='menubar']");

  //can add more conditions to check if it's on local finder page
  return (
    isInSearchPage &&
    searchForm &&
    appBar &&
    search &&
    (appBarRatingItems || appBarHoursItems)
  );
};

const getCategoryParent = () => {
  let elem = document.querySelector(
    "[data-attrid='kc:/local:lu attribute list']"
  );
  if (elem) {
    return elem;
  }
  elem = document.querySelector(
    ".kp-header .fYOrjf .wDYxhc .zloOqf > span.YhemCb"
  );
  if (elem) {
    return elem.parentElement;
  } else {
    elem = document.querySelector(".kp-header .fYOrjf .wDYxhc .zloOqf");
    if (elem) {
      return elem.parentElement;
    }
  }
};

const validateBusiness = () => {
  return (
    (isLocalFinder() && !businessName && getBusinessName()) ||
    (businessName && !isSameBusiness())
  );
};

const getBusinessName = () => {
  let elem = document.querySelector("[data-title]");
  if (elem) {
    return elem.getAttribute("data-title");
  }
  elem = document.querySelector("[data-attrid='title']");
  if (elem) {
    return elem.textContent;
  }
  elem = document.querySelector(
    ".kp-header .fYOrjf .Hhmu2e .Ftghae .SPZz6b > h2.qrShPb > span"
  );
  if (elem) {
    return elem.textContent.trim();
  }
};

const getDataPlaceValue = () => {
  let elem = document.querySelector("[data-place]");
  if (elem) {
    return elem.getAttribute("data-place");
  }
};

const generateSearchUrl = () =>
  `https://${getCurrentDomainHostName()}${getDataPlaceValue()}`;

const generateSearchUrlByLocation = () => {
  let address = document.querySelectorAll(
    "[data-attrid='kc:/location/location:address'] > div > div > span"
  )[1];
  if (address) {
    const encodedParam = encodeURI(
      `${getBusinessName()} ${address.textContent}`
    );
    return `https://${getCurrentDomainHostName()}/maps/search/${encodedParam}`;
  }
};

const fetchCategories = () => {
  callGoogleMap(generateSearchUrl(), (data) => {
    try {
      const processedTxt = data && data[3] ? data[3][6] : "";
      if (processedTxt && processedTxt.length > 5) {
        const windowArray = JSON.parse(processedTxt.substring(5));
        const categories = windowArray[6][13];

        if (categories && categories.length) {
          displayCategories(categories);
        } else {
          displayNoCategory(noCategoryMessage);
        }
      } else {
        displayNoCategory(noCategoryMessage);
      }
    } catch {
      displayNoCategory(noCategoryMessage);
    }
  });
};

const fetchCategoriesByNearLocation = () => {
  callGoogleMap(generateSearchUrlByLocation(), (data) => {
    try {
      const processedTxt = data && data[3] ? data[3][2] : "";

      if (processedTxt && processedTxt.length > 5) {
        const windowArray = JSON.parse(processedTxt.substring(5));

        let categories = [];
        let averageReviewRating, averageNumberOfReviews;

        try {
          categories = windowArray[0][1][0][14][13];

          if (windowArray[0][1][0][14][4] != null) {
            averageReviewRating = windowArray[0][1][0][14][4][7];
            averageNumberOfReviews = windowArray[0][1][0][14][4][8];
          } else {
            averageReviewRating = 0;
            averageNumberOfReviews = 0;
          }
        } catch {}

        try {
          categories = windowArray[0][1][1][14][13];

          if (windowArray[0][1][1][14][4] != null) {
            averageReviewRating = windowArray[0][1][1][14][4][7];
            averageNumberOfReviews = windowArray[0][1][1][14][4][8];
          } else {
            averageReviewRating = 0;
            averageNumberOfReviews = 0;
          }
        } catch {}

        if (
          categories &&
          categories.length &&
          averageNumberOfReviews ===
            getCurrentReviewRatingAndNumberOfReviews()["number-of-reviews"] &&
          parseInt(averageReviewRating * 100) ===
            parseInt(
              getCurrentReviewRatingAndNumberOfReviews()[
                "average-review-rating"
              ] * 100
            )
        ) {
          displayCategories(categories);
        } else {
          displayNoCategory(noCategoryMessage);
        }
      } else {
        displayNoCategory(noCategoryMessage);
      }
    } catch {
      displayNoCategory(noCategoryMessage);
    }
  });
};

const getCategoryContainer = () =>
  document.querySelector("." + Utility.gmbCategoryClass);

const removeGmb = () => {
  const gmbCategoryContainer = getCategoryContainer();
  if (gmbCategoryContainer) {
    gmbCategoryContainer.remove();
  }
};

const displayCategories = (catArr) => {
  try {
    removeGmb();

    const container = document.createElement("div");
    container.className = Utility.gmbCategoryClass;
    container.style.cssText = "position: relative; margin-top: 7px;";
    Utility.addTooltip(container);

    const title = document.createElement("span");
    title.textContent = "GMB Categories: ";
    title.style.cssText =
      "display: inline-block; font-size: 14px; border-radius: .25rem; color: #212529;";
    container.appendChild(title);

    catArr.forEach((v, i) => {
      let category = document.createElement("span");
      category.innerHTML =
      (i === 0 ? Utility.createEmojiIcon({iconName: 'star_icon.svg', iconSize: '12px'}): "") + v;
        
      category.style.cssText =
        "display: inline-block;" +
        "padding: .25em .4em;" +
        "font-size: 14px;" +
        "line-height: 1;" +
        "text-align: center;" +
        "white-space: nowrap;" +
        "vertical-align: baseline;" +
        "border-radius: .25rem;" +
        "color: #212529;" +
        "background-color: #ffc8b9;" +
        "margin: 0 0 3px 3px;";
      container.appendChild(category);
    });

    Utility.appendCategoryTool(container, catArr);
    
    getCategoryParent().appendChild(container);

    showAuditButton(getCategoryParent());
  } catch (e) {
    removeGmb();
    Logger.error(e);
  }
};

const showAuditButton = (parent) => {
  let buttonBasicAudit, buttonAIAudit, buttonReviewAudit, buttonPostAudit, buttonTeleport;
  const container = document.createElement("div");
  container.id = "popupButtonAdditionalInformationDiv";
  container.style.cssText = "text-align: left";
  const errorMessageDiv = document.createElement("div");
  errorMessageDiv.id = "errorMessageButtonClick";

  if (flag.localFinderBasicAudit === true) {
    buttonBasicAudit = Utility.createAuditDisplayButton(
      `${Utility.createEmojiIcon({iconName: 'rocket_icon.svg', iconSize: '14px'})} Basic`,
      "popupButtonAdditionalInformation"
    );
    buttonBasicAudit.style.width = "80px";
    addButtonOnClickLogic(buttonBasicAudit, "basic-audit");
    container.appendChild(buttonBasicAudit);
  }

  if (flag.localFinderAIAudit === true) {
    buttonAIAudit = Utility.createAuditDisplayButton(
      `${Utility.createEmojiIcon({iconName: 'brain_icon.svg', iconSize: '14px'})} AI`,
      "popupAIAudit"
    );
    buttonAIAudit.style.width = "80px";
    addButtonOnClickLogic(buttonAIAudit, "ai-audit");
    container.appendChild(buttonAIAudit);
  }

  if (flag.localFinderTeleport === true) {
    buttonTeleport = Utility.createAuditDisplayButton(
      `${Utility.createEmojiIcon({iconName: 'pin_icon.svg', iconSize: '14px'})} Teleport`,
      "popupTeleport");
    buttonTeleport.style.width = "80px";
    addButtonOnClickLogic(buttonTeleport, "teleport");
    container.appendChild(buttonTeleport);
  }

  if (flag.localFinderReviewAudit === true) {
    buttonReviewAudit = Utility.createAuditDisplayButton(
      `${Utility.createEmojiIcon({iconName: 'half_star_icon.svg', iconSize: '14px'})} Review`,
      "popupReviewAudit"
    );
    buttonReviewAudit.style.width = "80px";
    addButtonOnClickLogic(buttonReviewAudit, "review-audit");
    container.appendChild(buttonReviewAudit);
  }

  if (flag.localFinderPostAudit === true) {
    buttonPostAudit = Utility.createAuditDisplayButton(
      `${Utility.createEmojiIcon({iconName: 'megaphone_icon.svg', iconSize: '14px'})} Post`,
      "popupPostAudit"
    );
    buttonPostAudit.style.width = "80px";
    addButtonOnClickLogic(buttonPostAudit, "post-audit");
    container.appendChild(buttonPostAudit);
  }

  container.appendChild(errorMessageDiv);

  parent.appendChild(container);
};

let addButtonOnClickLogic = (buttonObject, auditType) => {
  buttonObject.onclick = function () {
    try {
      buttonObject.disabled = true;
      setTimeout(function () {
        buttonObject.disabled = false;
      }, 3000);
      AuditInformation.AuditInformationLogic(
        generateSearchUrl(),
        HowCorrectCategoryFoundEnum.ExactLink,
        HowCorrectCategoryFoundEnum,
        auditType
      );
    } catch (e) {
      document.getElementById("errorMessageButtonClick").innerText =
        "An error occured. Sorry for the inconvenience.";
    }
  };
};

const execute = () => {
  businessName = getBusinessName();
  displayLoading(getCategoryParent());
  fetchCategoriesWithDelay(fetchCategories, 1);
};

