let loopAndSeeIfMapSectionAnywhereOnPage = () => {
  let returnElement = null;
  let gimgElement = document.getElementsByTagName("g-img");
  for (let i = 0; i < gimgElement.length; i++) {
    let gimgCondition = false;
    try {
      gimgCondition =
        gimgElement[i] &&
        gimgElement[i].parentNode &&
        gimgElement[i].parentNode.parentNode &&
        gimgElement[i].parentNode.parentNode.parentNode &&
        gimgElement[i].parentNode.parentNode.parentNode.parentNode &&
        gimgElement[i].parentNode.parentNode.parentNode.parentNode.nextSibling &&
        gimgElement[i].parentNode.parentNode.parentNode.parentNode.nextSibling.nextSibling &&
        gimgElement[i].parentNode.parentNode.parentNode.parentNode.nextSibling.nextSibling
          .nextSibling &&
        gimgElement[
          i
        ].parentNode.parentNode.parentNode.parentNode.nextSibling.nextSibling.nextSibling.querySelectorAll(
          "a"
        )[0] &&
        gimgElement[i].parentNode.parentNode.parentNode.parentNode.nextSibling.nextSibling.nextSibling
          .querySelectorAll("a")[0]
          .hasAttribute("data-cid");
    } catch (e) {}
    if (gimgCondition) {
      returnElement = gimgElement[i];

      break;
    }
  }
  return returnElement;
};


